@use "../utilities" as *;

/*------------- wc-quote-top-area ------------ */
.wc-quote-top-area {
  background-color: var(--wc-light);
  background-image: url(./../images/shape/shape-church-noise.png);
  .wc-slideway-review {
    top: 0;
    left: 0;
    width: 100px;
    height: 100%;
    padding: 30px 30px;
    position: absolute;
    white-space: nowrap;
    writing-mode: sideways-lr;
    background-color: var(--wc-white);
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
