@use "sass:map";
@use "breakpoints" as *;

// media-breakpoint-up
@mixin media-breakpoint-up($name) {
  $value: map.get($breakpoints, $name);
  @if $value {
    @media (min-width: $value) {
      @content;
    }
  } @else {
    @warn "No breakpoint defined for `#{$name}`.";
  }
}

// media-breakpoint-down
@mixin media-breakpoint-down($name) {
  $value: map.get($breakpoints, $name);
  @if $value {
    @media (max-width: ($value - 0.02px)) {
      @content;
    }
  } @else {
    @warn "No breakpoint defined for `#{$name}`.";
  }
}

// media-breakpoint-between
@mixin media-breakpoint-between($min, $max) {
  $min-value: map.get($breakpoints, $min);
  $max-value: map.get($breakpoints, $max);

  @if $min-value and $max-value {
    @media (min-width: $min-value) and (max-width: $max-value) {
      @content;
    }
  } @else {
    @warn "No breakpoint defined for `#{$min}` or `#{$max}`.";
  }
}

// transition
@mixin transition($value) {
  -webkit-transition: all $value ease-out 0s;
  -moz-transition: all $value ease-out 0s;
  -ms-transition: all $value ease-out 0s;
  -o-transition: all $value ease-out 0s;
  transition: all $value ease-out 0s;
}

// transition-alt
@mixin transition-alt($values...) {
  -webkit-transition: $values;
  -moz-transition: $values;
  -ms-transition: $values;
  -o-transition: $values;
  transition: $values;
}

// transform
@mixin transform($value) {
  -webkit-transform: $value;
  -moz-transform: $value;
  -ms-transform: $value;
  -o-transform: $value;
  transform: $value;
}

// transform-origin
@mixin transform-origin($value) {
  -webkit-transform-origin: $value;
  -moz-transform-origin: $value;
  -ms-transform-origin: $value;
  -o-transform-origin: $value;
  transform-origin: $value;
}

// animation
@mixin animation($value) {
  -webkit-animation: $value;
  -moz-animation: $value;
  -ms-animation: $value;
  -o-animation: $value;
  animation: $value;
}

// border-radius
@mixin border-radius($value) {
  -webkit-border-radius: $value;
  -moz-border-radius: $value;
  -o-border-radius: $value;
  -ms-border-radius: $value;
  border-radius: $value;
}

// box-shadow
@mixin box-shadow($value) {
  -webkit-box-shadow: $value;
  -moz-box-shadow: $value;
  -ms-box-shadow: $value;
  -o-box-shadow: $value;
  box-shadow: $value;
}

// filter
@mixin filter($value) {
  -webkit-filter: $value;
  -moz-filter: $value;
  -ms-filter: $value;
  -o-filter: $value;
  filter: $value;
}

// backdrop-filter
@mixin backdrop-filter($value) {
  -webkit-backdrop-filter: $value;
  -moz-backdrop-filter: $value;
  -ms-backdrop-filter: $value;
  -o-backdrop-filter: $value;
  backdrop-filter: $value;
}

// background-clip
@mixin background-clip($value) {
  -webkit-background-clip: $value;
  -moz-background-clip: $value;
  -ms-background-clip: $value;
  -o-background-clip: $value;
  background-clip: $value;
}
