@use "../utilities" as *;

/*------------- wc-pricing-plan-area-01 ------------ */
.wc-pricing-plan-area-01 {
  .wc-pricing-col {
    border-right: 1px solid var(--bs-border-color);
    @include transition(0.3s);
    &:hover {
      @include box-shadow(var(--wc-shadow-lg));
    }
    &:last-child {
      border-right: none;
    }
    @include media-breakpoint-down(xl) {
      &:is(:nth-child(1), :nth-child(2), :nth-child(3)) {
        border-right: none;
        border-bottom: 1px solid var(--bs-border-color);
      }
      &:is(:nth-child(1), :nth-child(3)) {
        border-right: 1px solid var(--bs-border-color);
      }
      &:is(:nth-child(3)) {
        border-bottom: none;
      }
    }
    @include media-breakpoint-down(md) {
      &:is(:nth-child(3)) {
        border-bottom: 1px solid var(--bs-border-color);
      }
    }
  }
}

/*------------- wc-pricing-plan-area-02 ------------ */
.wc-pricing-plan-area-02 {
  .wc-pricing-figure,
  .wc-pricing-period {
    @include transition(0.3s);
  }
  .wc-pricing-switch {
    a.active {
      color: var(--wc-primary);
      background-color: var(--wc-light-alt);
    }
  }
}

/*------------- wc-pricing-plan-area-03 ------------ */
.wc-pricing-plan-area-03 {
  .wc-pricing-plan-card {
    .wc-pricing-plan-card-header {
      position: relative;
      &::before {
        top: 0;
        left: 0;
        z-index: 1;
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        background: rgba(50, 75, 70, 0.75) url(./../images/shape/shape-dots-grid.png);
        @include border-radius(12px 12px 0 0);
      }
    }
    &:hover {
      .wc-pricing-plan-card-img {
        @include transform(scale(1.025));
      }
    }
  }
}
