@use "../utilities" as *;

/*------------- wc-history-header-area ------------ */
.wc-history-header-area {
  background-color: var(--wc-light);
  background-image: url(./../images/shape/shape-church-noise.png);
  position: relative;
  &::before {
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: url(../images/shape/shape-grid-xs.png);
    background-repeat: no-repeat;
  }
}

/*------------- wc-history-timeline ------------ */
.wc-history-timeline {
  padding: 0;
  width: 100%;
  position: relative;
  &::after {
    top: 0;
    bottom: 0;
    left: 50%;
    width: 1px;
    content: "";
    position: absolute;
    margin-left: -1px;
    background: var(--bs-border-color);
    @include media-breakpoint-down(lg) {
      left: 25px;
    }
  }
  .wc-timeline-item {
    width: 50%;
    position: relative;
    background: inherit;
    @include media-breakpoint-down(lg) {
      width: 100%;
    }
    &:nth-child(odd) {
      right: 0;
      padding-inline-end: 90px;
      @include media-breakpoint-down(lg) {
        padding-top: 120px;
        padding-inline-end: 0px;
        margin-bottom: 24px;
      }
      .wc-timeline-data {
        font-size: 20px;
        right: 0;
        @include media-breakpoint-down(lg) {
          right: auto;
          left: 15px;
          top: 50px;
        }
        &::after {
          top: 49px;
          bottom: 0;
          right: 95px;
          height: 1px;
          width: 41px;
          content: "";
          position: absolute;
          background: var(--bs-border-color);
          z-index: -1;
        }
      }
    }
    &:nth-child(even) {
      left: 50%;
      padding-inline-start: 90px;
      @include media-breakpoint-down(lg) {
        left: 0px;
        padding-inline-start: 0px;
        margin-top: 160px;
        margin-bottom: 35px;
      }
      .wc-timeline-data {
        font-size: 20px;
        left: -100px;
        @include media-breakpoint-down(lg) {
          top: -70px;
          left: 15px;
        }

        &::after {
          top: 49px;
          bottom: 0;
          left: 95px;
          height: 1px;
          width: 41px;
          content: "";
          position: absolute;
          background: var(--bs-border-color);
          z-index: -1;
          @include media-breakpoint-down(lg) {
            left: -45px;
            right: 0;
          }
        }
      }
    }
    .wc-timeline-data {
      font-size: 20px;
      font-weight: 700;
      color: var(--wc-primary);
      font-family: var(--wc-ff-number);
      width: 100px;
      height: 100px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      background-color: var(--wc-brand);
      border: 5px solid var(--wc-light);
      @include transform(translate(50%, -50%));
      @include border-radius(50%);
      @include transition(0.3s);
      position: absolute;
      z-index: 2;
      top: 50%;
    }
    .wc-timeline-content {
      z-index: 3;
      position: relative;
      @include box-shadow(var(--wc-shadow-modern));
      @include border-radius(4px);
      @include transition(0.3s);
      .wc-line-shape {
        height: 2px;
        width: 100px;
        margin-right: 5px;
        margin-bottom: 5px;
        display: inline-block;
        background: linear-gradient(90deg, #f8f9fa 0%, #004d38 100%);
        @include border-radius(50px);
      }
    }
    &:hover {
      .wc-timeline-data {
        color: var(--bs-white);
        background-color: var(--wc-primary);
      }
      .wc-timeline-content {
        @include box-shadow(var(--wc-shadow-lg));
      }
    }
  }
}
