@use "../utilities" as *;

/*------------- wc-hero-area-three ------------ */
.wc-hero-area-three {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  padding-top: 175px;
  padding-bottom: 75px;
  margin-top: -100px;
  min-height: calc(100vh - 45px);

  &::before {
    top: 0;
    left: 0;
    content: "";
    width: 50%;
    height: 100%;
    position: absolute;
    background: var(--wc-primary) url(../images/shape/shape-grid-lg.png);
    background-repeat: no-repeat;
    @include media-breakpoint-down(xl) {
      width: 100%;
      height: 50%;
    }
  }

  &::after {
    content: "";
    position: absolute;
    left: auto;
    top: 0;
    right: 0;
    bottom: 0;
    width: 50%;
    background: url("./../images/shape/shape-triangle-object.png"), linear-gradient(0deg, rgba(236, 235, 65, 0.8) 0%, rgba(1, 89, 65, 0.8) 100%);
    background-size: auto;
    background-position: right bottom;
    background-repeat: no-repeat;
    @include media-breakpoint-down(xl) {
      width: 100%;
    }
  }

  .wc-hero-book-btn-wrap {
    left: 0;
    top: 50%;
    z-index: 4;
    position: absolute;
    white-space: nowrap;
    writing-mode: sideways-lr;
    @include transform(translateY(-50%));
    @include media-breakpoint-down("3xl") {
      right: 0;
      top: 100px;
      left: calc(50% - 53px);
      @include transform(translateY(0));
    }
    @include media-breakpoint-down(xl) {
      left: 0;
      top: 50%;
      right: auto;
      @include transform(translateY(0));
    }
    @include media-breakpoint-down(sm) {
      display: none;
    }
    .wc-hero-book-btn {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 25px 15px;
      color: var(--wc-brand);
      background-color: var(--wc-primary-alt);
      font-size: 13px;
      word-spacing: 4px;
      text-transform: uppercase;
      @include transition(0.3s);
      // &:hover {
      //   padding-left: 20px;
      //   padding-right: 20px;
      // }
    }
  }

  .wc-hero-big-text {
    left: 0;
    right: auto;
    bottom: 0px;
    position: absolute;
    margin: 0 auto;
    .text-stroke {
      margin: 0;
      opacity: 0.2;
      line-height: 1;
      font-size: 200px;
      font-weight: 900;
      text-align: center;
      text-transform: uppercase;
      stroke-width: 0.75px;
      -webkit-text-stroke-width: 0.75px;
    }
  }

  .wc-hero-follow-us {
    gap: 12px;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 0;
    top: 100px;
    padding: 20px;
    position: absolute;
    white-space: nowrap;
    writing-mode: sideways-lr;
    @include media-breakpoint-between(md, lg) {
      display: none;
    }
    @include media-breakpoint-down(md) {
      top: auto;
      bottom: 0;
      width: 100%;
      writing-mode: horizontal-tb;
      background-color: rgba(0, 0, 0, 0.05);
      @include backdrop-filter(blur(8px));
      @include transform(none);
    }

    .wc-hero-follow-text {
      font-size: 13px;
      font-weight: 700;
      text-transform: uppercase;
      color: var(--bs-white);
    }

    .wc-hero-follow-line {
      width: 1px;
      height: 25px;
      background: var(--bs-white);
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .wc-hero-follow-link {
      a {
        font-size: 16px;
        padding: 12px 15px;
        color: var(--bs-white);
        display: inline-block;
        @include transition(0.3s);
        &:hover {
          color: var(--wc-brand);
        }
      }
    }
  }
  .moving-particles {
    width: 50% !important;
    @include media-breakpoint-down(xl) {
      width: 100% !important;
    }
  }
}

/*------------- wc-about-area-03 ------------ */
.wc-about-area-03 {
  .wc-finance-about-img {
    img {
      @include media-breakpoint-down(xxl) {
        @include border-radius(0 6px 6px 0);
      }
    }
  }
  .wc-finance-about-review {
    top: 0;
    right: -100px;
    width: 100px;
    height: 100%;
    padding: 60px 30px 30px;
    position: absolute;
    white-space: nowrap;
    writing-mode: sideways-lr;
    background-color: var(--wc-light);
    display: flex;
    align-items: center;
    justify-content: center;
    @include border-radius(0 6px 6px 0);
    @include media-breakpoint-up(xxl) {
      justify-content: end;
    }
    @include media-breakpoint-down(xxl) {
      right: 0;
    }
  }
  .wc-about-experience {
    width: 200px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    position: absolute;
    top: 50%;
    left: -45px;
    background: var(--wc-primary);
    @include border-radius(50% 0 0 50%);
    @include transform(translate(-50%, -50%));
  }
}

/*------------- wc-features-area ------------ */
.wc-features-area {
  position: relative;
  .wc-features-content-col {
    position: relative;
    padding: 0 40px;
    @include media-breakpoint-between(md, xl) {
      max-width: calc(100% - 200px);
      margin-left: auto;
    }
    @include media-breakpoint-down(xl) {
      padding: 0 32px;
    }
    @include media-breakpoint-down(sm) {
      padding: 0 15px;
    }
    &::before {
      top: 0;
      left: 0;
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      background: url(../images/shape/shape-grid-xs.png);
      background-repeat: no-repeat;
    }
    .wc-features-big-text {
      top: 0px;
      left: -20px;
      width: 200px;
      height: 100%;
      position: absolute;
      white-space: nowrap;
      writing-mode: sideways-lr;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: var(--wc-brand);
      @include media-breakpoint-between(xxl, "3xl") {
        display: none;
      }
      @include media-breakpoint-between(md, xl) {
        left: -200px;
      }
      @include media-breakpoint-down(md) {
        display: none;
      }
    }
  }
  .wc-features-video-col {
    position: relative;
    overflow: hidden;
    &::before {
      top: 0;
      left: 0;
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      background: rgba(0, 90, 65, 0.5) url(./../images/shape/shape-dots-grid.png);
    }
    .wc-features-bg-video {
      object-fit: cover;
      z-index: -100;
      background-position: 100%;
      background-size: cover;
      width: 100%;
      height: 100%;
      margin: auto;
      inset: -100%;
    }
    .wc-features-projects {
      left: 50%;
      bottom: 0;
      position: absolute;
      width: 300px;
      height: 300px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-direction: column;
      background: rgba(0, 77, 56, 0.5);
      @include transform(translate(-50%, 50%));
      @include border-radius(100%);
      @include backdrop-filter(blur(8px));
      @include media-breakpoint-down(sm) {
        width: 200px;
        height: 200px;
      }
      i {
        padding: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 45px;
        height: 45px;
        margin-top: -20px;
        color: var(--wc-primary);
        background: var(--wc-brand);
        @include border-radius(100%);
      }
    }
  }
}

/*------------- wc-services-area-03 ------------ */
.wc-services-area-03 {
  position: relative;
  &::before {
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: url(../images/shape/shape-grid-lg.png);
    background-repeat: no-repeat;
  }
  .wc-services-swiper-03 {
    margin-right: -25vw;
    @include media-breakpoint-down(xxl) {
      margin-right: -25vw;
    }
    @include media-breakpoint-down(xl) {
      margin-right: 0;
    }
    .swiper-slide {
      .card-body {
        z-index: 3;
        position: relative;
        &::before {
          width: 30px;
          height: 30px;
          content: "";
          bottom: 0;
          right: 0;
          position: absolute;
          background-color: var(--wc-primary);
          @include transition(0.3s);
        }
      }
      &:hover {
        .card-body {
          &::before {
            width: 100%;
            height: 100%;
          }
          .service-content {
            .card-title,
            .card-text,
            .card-link {
              color: var(--wc-white) !important;
            }
            .card-link {
              &:hover {
                color: var(--wc-brand) !important;
              }
            }
          }
        }
        .bg-theme-primary {
          background-color: var(--wc-brand) !important;
          .text-stroke-light {
            -webkit-text-stroke-color: var(--bs-dark) !important;
          }
        }
      }
    }
  }
}

/*------------- wc-parallax-contact-area ------------ */
.wc-parallax-contact-area {
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-image: url(../images/bg/bg-man-with-phone.jpg);
  display: flex;
  align-items: center;
  justify-content: center;
  .wc-parallax-contact-form {
    position: relative;
    &::before {
      top: 0;
      left: 0;
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      background: url(../images/shape/shape-grid-xs.png);
      background-repeat: no-repeat;
    }
  }
}

/*------------- sliding-content-list ------------ */
.sliding-content-list {
  gap: 25px;
  display: flex;
  height: 100%;
  width: 100%;
  overflow: hidden;
  @include media-breakpoint-down(xl) {
    flex-direction: column;
  }
  @include media-breakpoint-down(sm) {
    gap: 12px;
  }
  .sliding-item {
    flex: 0 0 128px;
    height: 550px;
    overflow: hidden;
    background-color: var(--wc-light);
    @include transition(0.3s);
    @include border-radius(8px);
    @include media-breakpoint-down(xxl) {
      height: 450px;
    }
    @include media-breakpoint-down(md) {
      flex: 0 0 90px;
    }
    .sliding-item-heading {
      height: 100%;
      white-space: nowrap;
      writing-mode: sideways-lr;
      display: flex;
      align-items: center;
      justify-content: start;
      cursor: pointer;
      padding: 20px 40px 30px;
      gap: 20px;
      @include media-breakpoint-down(xl) {
        writing-mode: horizontal-tb;
      }
    }
    .sliding-item-content {
      padding: 30px 30px;
      @include media-breakpoint-down(sm) {
        padding: 15px 15px;
      }
    }

    &.active {
      flex: 0 0 calc(100% - 615px);
      .sliding-item-heading {
        height: auto;
        padding-top: 25px;
        padding-bottom: 30px;
        align-items: baseline;
        white-space: nowrap;
        writing-mode: horizontal-tb;
        border-bottom: 1px solid var(--wc-border-alt);
      }
      .sliding-item-content {
        display: flex;
        align-items: center;
        justify-content: center;
        height: calc(100% - 110px);
      }
    }
  }
}

/*------------- wc-our-skills-area ------------ */
.wc-our-skills-area {
  .image-reveal-effect {
    position: relative;
    overflow: hidden;
    .wc-features-projects {
      left: 50%;
      bottom: 0;
      position: absolute;
      width: 300px;
      height: 300px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-direction: column;
      background: rgba(0, 77, 56, 0.5);
      @include transform(translate(-50%, 50%));
      @include border-radius(100%);
      @include backdrop-filter(blur(8px));
      i {
        padding: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 45px;
        height: 45px;
        margin-top: -20px;
        color: var(--wc-primary);
        background: var(--wc-brand);
        @include border-radius(100%);
      }
      h1 {
        font-weight: 900;
        color: var(--wc-white);
        margin-top: 20px;
        margin-bottom: 0px;
      }
      p {
        margin-bottom: 0;
        color: var(--wc-white);
      }
    }
  }
}

/*------------- wc-contact-text-area ------------ */
.wc-contact-text-area {
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-image: url(../images/bg/bg-big-teamwork.jpg);
  display: flex;
  align-items: center;
  justify-content: center;
  &::before {
    top: 0;
    left: 0;
    z-index: 1;
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(0, 90, 65, 0.5) url(./../images/shape/shape-dots-grid.png);
  }
  &::after {
    top: 50%;
    left: 50%;
    z-index: 1;
    content: "";
    width: 800px;
    height: 800px;
    position: absolute;
    background: rgba(0, 90, 65, 0.5);
    @include border-radius(100%);
    @include transform(translate(-50%, -50%));
  }
  #cursor-follower {
    top: 0;
    left: 0;
    z-index: 9999;
    width: 200px;
    height: 200px;
    position: absolute;
    padding: 30px 30px;
    pointer-events: auto;
    background-color: var(--wc-brand);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    @include border-radius(50%);
    @include box-shadow(var(--wc-shadow-lg));
    @include transform(translate(-50%, -50%));
    @include transition(0.3s);
    @include media-breakpoint-down(lg) {
      padding: 20px 20px;
    }
    .cursor-follower-link {
      width: 100%;
      height: 100%;
      font-size: 12px;
      font-weight: 700;
      text-align: center;
      text-transform: uppercase;
      color: var(--wc-primary);
      text-decoration: none;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
    }
    @include media-breakpoint-down(xl) {
      width: 180px;
      height: 180px;
    }
    @include media-breakpoint-down(lg) {
      width: 150px;
      height: 150px;
    }
    @include media-breakpoint-down(md) {
      width: 120px;
      height: 120px;
    }
    @include media-breakpoint-down(sm) {
      width: 100px;
      height: 100px;
    }
  }
}
