@use "../utilities" as *;

/*------------- wc-hero-area-two ------------ */
.wc-hero-area-two {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  min-height: 100vh;
  padding-top: 175px;
  padding-bottom: 75px;
  margin-top: -100px;
  &::before,
  &::after {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: "";
    position: absolute;
  }
  &::before {
    background: linear-gradient(to bottom, var(--wc-primary), var(--wc-white));
  }
  &::after {
    background: url(./../images/shape/shape-hero-2-pattern.png);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 0.05;
  }
  .shape-arrow-circle {
    position: absolute;
    bottom: -60px;
    left: -120px;
    z-index: 0;
    @include media-breakpoint-down(xxl) {
      left: -100px;
    }
    @include media-breakpoint-down(xl) {
      left: 65px;
    }
    @include media-breakpoint-down(lg) {
      left: 50px;
    }
    @include media-breakpoint-down(md) {
      left: 15px;
    }
    @include media-breakpoint-down(sm) {
      left: -40px;
    }
  }
  .wc-hero-img {
    max-width: 450px;
    margin-left: auto;
    position: relative;
    bottom: -100px;
    @include media-breakpoint-down(xl) {
      margin: auto;
    }
    img {
      z-index: 2;
      position: relative;
    }
    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background-color: transparent;
      background: linear-gradient(to bottom, var(--wc-primary), var(--wc-white));
      @include border-radius(1000px 1000px 0px 0px);
      // @include transform(translateX(-25px));
    }
  }
  .wc-hero-cousult-badge {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    text-align: center;
    top: 25px;
    left: -50px;
    padding: 25px;
    z-index: 9999;
    width: 150px;
    height: 150px;
    color: var(--wc-primary);
    background-color: var(--wc-brand);
    @include border-radius(50%);
  }
  .wc-hero-follow-us {
    gap: 12px;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 0;
    bottom: 30px;
    padding: 20px;
    position: absolute;
    white-space: nowrap;
    writing-mode: sideways-lr;
    @include media-breakpoint-between(xl, xxl) {
      display: none;
    }
    @include media-breakpoint-down(md) {
      top: auto;
      bottom: 0;
      width: 100%;
      writing-mode: horizontal-tb;
      background-color: rgba(0, 0, 0, 0.05);
      @include backdrop-filter(blur(8px));
      @include transform(none);
    }
    .wc-hero-follow-text {
      font-size: 12px;
      font-weight: 700;
      text-transform: uppercase;
      color: var(--bs-primary);
      @include media-breakpoint-down(md) {
        color: var(--bs-white);
      }
    }
    .wc-hero-follow-line {
      width: 1px;
      height: 25px;
      background: var(--bs-primary);
      display: flex;
      align-items: center;
      justify-content: center;
      @include media-breakpoint-down(md) {
        background: var(--bs-white);
      }
    }
    .wc-hero-follow-link {
      a {
        padding: 10px;
        line-height: 0;
        color: var(--bs-primary);
        display: inline-block;
        @include media-breakpoint-down(md) {
          color: var(--bs-white);
        }
      }
    }
  }
}

/*------------- wc-features-area ------------ */
.wc-features-area {
  .section-header {
    margin-left: 0px;
    @include media-breakpoint-between(md, xxl) {
      margin-left: 120px;
    }
  }
  .wc-features-bottom-card {
    .wc-features-video-item {
      height: 100%;
      min-height: 450px;
      background: url(./../images/bg/bg-banner-play.jpg) no-repeat center center;
      background-size: cover;
      display: flex;
      align-items: start;
      justify-content: end;
      flex-direction: column;
      position: relative;
      padding: 25px 25px;
      @include border-radius(8px);
      @include transition(0.3s);
      @include media-breakpoint-down(sm) {
        padding: 13px 13px;
      }
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(to top, rgba(0, 70, 50, 0.9), rgba(255, 255, 255, 0.1));
        @include border-radius(8px);
      }
      .wc-play-btn {
        width: 50px;
        height: 50px;
        color: var(--wc-dark);
        background-color: var(--wc-brand);
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 25px;
        @include border-radius(50%);
      }
      .pulse-effect {
        isolation: isolate;
        transition: all 0.25s cubic-bezier(0, 0, 0.31, 0.99);
        animation: pulse-effect 2s infinite;
        @keyframes pulse-effect {
          0% {
            box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.5);
          }
          70% {
            box-shadow: 0 0 0 25px rgba(255, 255, 255, 0);
          }
          100% {
            box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
          }
        }
      }
    }
    .wc-features-count-item {
      z-index: 3;
      height: 100%;
      min-height: 450px;
      display: flex;
      align-items: start;
      justify-content: space-between;
      flex-direction: column;
      position: relative;
      padding: 25px 25px;
      background: var(--wc-primary);
      @include border-radius(8px);
      @include transition(0.3s);      
      @include media-breakpoint-down(sm) {
        padding: 13px 13px;
      }
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        @include border-radius(8px);
        background: url(../images/shape/shape-grid-brand.png);
        background-position: bottom right;
        background-repeat: no-repeat;
      }
      .wc-features-count-item-bottom {
        p {
          color: var(--wc-white);
          font-size: 16px;
          line-height: 1.5;
          margin-top: 10px;
        }
      }
    }
    .wc-features-services-item {
      height: 100%;
      min-height: 450px;
      display: flex;
      align-items: start;
      justify-content: end;
      flex-direction: column;
      position: relative;
      padding: 25px 25px;
      background: url(./../images/bg/bg-banner-services.jpg) no-repeat center center;
      background-size: cover;
      @include border-radius(8px);
      @include transition(0.3s);      
      @include media-breakpoint-down(sm) {
        padding: 13px 13px;
      }
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(to top, rgba(0, 70, 50, 0.9), rgba(255, 255, 255, 0.1));
        @include border-radius(8px);
      }
      .services-item {
        z-index: 2;
        position: relative;
        display: inline-flex;
        @include transition(0.3s);
        a {
          font-size: 13px;
          padding: 4px 12px;
          color: var(--wc-white);
          background-color: #ffffff30;
          @include border-radius(50px 50px);
        }
        &:hover {
          a {
            color: var(--wc-primary);
            background-color: var(--wc-brand);
          }
        }
      }
    }
  }
  .corporate-features-review {
    top: 0;
    left: 0;
    width: 100px;
    height: 420px;
    padding: 30px 30px;
    position: absolute;
    white-space: nowrap;
    writing-mode: sideways-lr;
    background-color: var(--wc-light);
    display: flex;
    align-items: center;
    justify-content: center;
    @include media-breakpoint-up(lg) {
      height: 335px;
    }
    @include media-breakpoint-up(xxl) {
      height: 480px;
    }
  }
}

/*------------- wc-howit-work-area ------------ */
.wc-howit-work-area {
  clip-path: ellipse(125% 100% at bottom);
  background-image: url(../images/shape/vr-line-sm-primary.svg);
  @include media-breakpoint-down(xxl) {
    clip-path: ellipse(150% 100% at bottom);
  }
  @include media-breakpoint-down(xl) {
    clip-path: ellipse(175% 100% at bottom);
  }
  @include media-breakpoint-down(lg) {
    clip-path: ellipse(200% 100% at bottom);
  }
  @include media-breakpoint-down(md) {
    clip-path: ellipse(225% 100% at bottom);
  }
  @include media-breakpoint-down(sm) {
    clip-path: ellipse(250% 100% at bottom);
  }
  .wc-howit-work-card {
    position: relative;
    @include transition(0.3s);
    .wc-howit-work-figure {
      position: relative;
      background-size: cover;
    }
    .wc-howit-work-figure-01 {
      background-image: url(./../images/banner/sm-01.jpg);
    }
    .wc-howit-work-figure-02 {
      background-image: url(./../images/banner/sm-02.jpg);
    }
    .wc-howit-work-figure-03 {
      background-image: url(./../images/banner/sm-03.jpg);
    }
    .wc-howit-work-figure-04 {
      background-image: url(./../images/banner/sm-04.jpg);
    }
    .wc-howit-work-count {
      top: 80%;
      left: 35%;
      position: absolute;
      @include transition(0.3s);
    }
    &:hover {
      @include transform(translateY(-5px));
      .wc-howit-work-count {
        top: 0;
        left: 0;
        position: relative;
        width: 180px !important;
        height: 180px !important;
        transform: translate(0);
        align-items: center !important;
        font-size: 60px !important;
      }
    }
  }
}

/*------------- wc-services-area-02 ------------ */
.wc-services-area-02 {
  .service-item {
    .card-body {
      &::before {
        width: 30px;
        height: 30px;
        content: "";
        bottom: 0;
        left: 0;
        position: absolute;
        background-color: var(--wc-light);
        @include transition(0.3s);
      }
    }
    &:hover {
      .card-body {
        &::before {
          width: 100%;
          height: 100%;
        }
        .service-content {
          z-index: 3;
          position: relative;
        }
        .service-link {
          z-index: 3;
          position: relative;
        }
      }
    }
  }
}

/*------------- wc-cta-area ------------ */
.wc-cta-area {
  .wc-cta-image {
    width: 100%;
    height: 100%;
    min-height: 600px;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: left center;
    background-image: url(../images/bg/bg-banner-meeting.jpg);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .wc-cta-form {
    position: relative;
    background: var(--wc-light);
    &::before {
      top: 0;
      left: 0;
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      background: url(../images/shape/shape-grid-xs.png);
      background-repeat: no-repeat;
    }
  }
}

/*------------- wc-case-study-area ------------ */
.wc-case-study-area {
  .wc-case-study-items {
    .wc-case-study-item {
      margin-bottom: 25px;
      position: sticky;
      z-index: 1020;
      top: 130px;
      background-color: var(--wc-light);
      @include border-radius(8px);
      @include transition(0.3s);
      @include media-breakpoint-down(md) {
        position: inherit;
      }
      .card-header {
        min-height: 150px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        border-top: 1px solid var(--bs-border-color);
      }
    }
  }
}

/*------------- wc-marquee-slider-primary ------------ */
.wc-marquee-slider-primary {
  background-color: var(--wc-primary);
  position: relative;
  &::before,
  &::after {
    top: 0;
    content: "";
    position: absolute;
    width: 330px;
    height: 100%;
    z-index: 2;
    @include media-breakpoint-down(lg) {
      width: 200px;
    }
    @include media-breakpoint-down(md) {
      width: 130px;
    }
  }
  &::before {
    left: 0;
    background: linear-gradient(90deg, var(--wc-primary) 0%, var(--wc-primary) 10%, #ffffff00 100%);
  }
  &::after {
    right: 0;
    background: linear-gradient(270deg, var(--wc-primary) 0%, var(--wc-primary) 10%, #ffffff00 100%);
  }
  .marquee-slider {
    .marquee-group {
      &.bg-dot-style {
        .marquee-item {
          .marquee-icon {
            background-color: var(--wc-brand);
          }
          .marquee-text {
            color: var(--wc-brand);
          }
          &:nth-child(odd) {
            .marquee-text {
              mix-blend-mode: normal;
              color: transparent;
              stroke-width: 1px;
              -webkit-text-stroke-width: 1px;
              -webkit-text-stroke-color: var(--wc-brand);
            }
          }
        }
      }
    }
  }
}
