@use "../utilities" as *;

/*------------- wc-team-style-area-01 ------------ */
.wc-team-style-area-01 {
  background-repeat: repeat;
  background-image: url(./../images/shape/shape-aper-grid.jpg);
  .team-card {
    border: none;
    overflow: hidden;
    position: relative;
    @include box-shadow(var(--wc-shadow-modern));
    @include transition(0.3s);
    .team-image {
      .team-social-info {
        gap: 8px;
        padding: 20px;
        display: flex;
        align-items: center;
        flex-direction: column;
        background-color: var(--wc-brand);
        position: absolute;
        top: 0;
        right: 0;
        opacity: 0;
        visibility: hidden;
        @include transition(0.3s);
        @include border-radius(0 0 0 4px);
        @include transform(translateX(100%));

        a {
          width: 32px;
          height: 32px;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }

      img {
        @include transition(0.3s);
      }
    }

    .team-content {
      z-index: 9;
      width: 100%;
      text-align: center;
      padding: 32px 24px;
      position: relative;
      background-color: var(--wc-white);
      @include transition(0.3s);

      &::after {
        inset: 0;
        z-index: -1;
        content: "";
        position: absolute;
        opacity: 0;
        visibility: hidden;
        background-color: var(--wc-brand);
        @include transform(translateY(100%));
        @include transition(0.3s);
      }
    }

    &:hover {
      @include box-shadow(var(--wc-shadow-lg));
      .team-image {
        .team-social-info {
          opacity: 1;
          visibility: visible;
          @include transform(translateX(0));
        }

        img {
          @include transform(scale(1.025));
        }
      }

      .team-content {
        &::after {
          opacity: 1;
          visibility: visible;
          @include transform(translateY(0));
        }
      }
    }
  }
  &.wc-team-style-light {
    .team-content {
      background-color: var(--wc-light);
    }
  }
  .wc-team-join-link {
    top: 50%;
    right: 10px;
    z-index: 2;
    position: absolute;
    white-space: nowrap;
    writing-mode: sideways-lr;
    @include transform(translateY(-50%));
  }
}

