/*------------- wc-professional-skills ------------ */
.wc-professional-skills {
  .skill-set {
    padding-bottom: 25px;
    margin-bottom: 25px;
    border-bottom: 1px dashed var(--bs-border-color);
    &:last-child {
      padding-bottom: 0px;
      margin-bottom: 0px;
      border-bottom: none;
    }
    .skill-bar {
      position: relative;
      background-color: var(--bs-border-color);
      @include border-radius(50px);
      .skill-bar-in {
        height: 6px;
        position: relative;
        background: linear-gradient(to right, var(--wc-light), var(--wc-primary));
        @include transition(0.3s);
        @include border-radius(50px);
        span {
          right: -15px;
          bottom: 15px;
          font-size: 10px;
          padding: 0px 8px;
          position: absolute;
          color: var(--bs-white);
          background: var(--bs-primary);
          @include border-radius(50px);
          &:after {
            content: "";
            right: 10px;
            bottom: -8px;
            position: absolute;
            border-top: 8px solid var(--bs-primary);
            border-left: 6px solid transparent;
            border-right: 6px solid transparent;
          }
        }
      }
    }
  }
  .team-details-contact-form {
    padding: 50px 50px;
    background-color: var(--wc-light);
    background-image: url(../images/shape/shape-church-noise.png);
    @include border-radius(4px 4px);
  }
}
