@use "../utilities" as *;

/*------------- wc-contact-us-v-01 ------------ */
.wc-contact-card-area {
  background-color: var(--wc-light);
  background-image: url(./../images/shape/shape-church-noise.png);
  position: relative;
  z-index: 2;
  &::before {
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: url(./../images/shape/shape-border-grid-02.png);
    background-repeat: no-repeat;
  }
  .card {
    .wc-contact-card-icon {
      width: 80px;
      height: 80px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      overflow: hidden;
      background: var(--bs-white);
      border: 1px dashed var(--wc-light);
      @include border-radius(100%);
      @include transition(0.3s);
      &::before {
        top: 0;
        left: -45%;
        width: 100%;
        height: 100%;
        content: "";
        position: absolute;
        background: var(--wc-brand);
        @include transition(0.3s);
      }
      i {
        z-index: 3;
        position: relative;
      }
    }
    &:hover {
      .wc-contact-card-icon {
        border-color: var(--wc-brand-alt);
        &::before {
          left: 0;
          @include border-radius(100%);
        }
      }
    }
  }
}
.wc-contact-page {
  .wc-contact-form-area {
    z-index: 2;
    position: relative;

    &::before {
      top: 0;
      left: 0;
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      background: url(./../images/shape/shape-map-dots.png);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: auto;
    }
    .container {
      z-index: 2;
      position: relative;
    }
  }
  .wc-footer-area {
    .wc-footer-top {
      &::before {
        background-color: transparent;
      }
    }
  }
}

/*------------- wc-contact-us-v-03 ------------ */
.wc-branch-card-area,
.wc-faqs-area-03 {
  &::before {
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: url(../images/shape/shape-grid-xl.png);
    background-repeat: no-repeat;
    background-position: top center;
  }
}
