@use "../utilities" as *;

/*------------- wc-hero-area-four ------------ */
.wc-hero-area-four {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  padding-top: 75px;
  padding-bottom: 75px;
  min-height: calc(100vh - 140px);
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-image: url(./../images/bg/bg-banner-life.jpg);
  &::before {
    top: 0;
    left: 0;
    z-index: 1;
    content: "";
    width: 530px;
    height: 100%;
    position: absolute;
    background: var(--wc-primary-alt) url(./../images/shape/shape-wave-line-alt.png);
    @include media-breakpoint-between(xxl, "3xl") {
      width: 370px;
    }
    @include media-breakpoint-down(xxl) {
      width: 270px;
    }
    @include media-breakpoint-down(xl) {
      width: 50%;
    }
    @include media-breakpoint-down(lg) {
      width: 0;
    }
  }
  &::after {
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(50, 75, 70, 0.5) url(./../images/shape/shape-dots-grid.png);
  }
  .wc-hero-big-text-style {
    position: relative;
    padding: 0 20px 5px;
    font-weight: 700;
    color: var(--wc-primary);
    background: var(--wc-brand);
    @include border-radius(50px);
    height: 80px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    @include media-breakpoint-down(md) {
      height: 70px;
    }
    @include media-breakpoint-down(sm) {
      height: 60px;
    }
  }
}

/*------------- wc-feature-area ----------- */
.wc-feature-area {
  .wc-feature-item {
    .wc-feature-icon {
      width: 60px;
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: var(--wc-brand);
      @include border-radius(50%);
      @include transition(0.3s);
      img {
        width: 30px;
        height: 30px;
        align-content: center;
        justify-content: center;
        position: relative;
        z-index: 2;
      }
    }
    &:hover {
      .wc-feature-icon {
        background-color: var(--wc-brand-alt);
      }
    }
  }
}

/*------------- wc-about-area-four ----------- */
.wc-about-area-four {
  .wc-small-card {
    overflow: hidden;
    position: relative;
    .card-body {
      height: 100%;
      min-height: 300px;
      display: flex;
      flex-direction: column;
      align-items: start;
      justify-content: center;
    }
    .wc-small-slideway-text {
      position: absolute;
      width: 30px;
      height: 100%;
      white-space: nowrap;
      writing-mode: sideways-lr;
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      font-family: var(--ff-number);
      background: var(--wc-primary);
    }
  }
}

/*------------- wc-insurance-service-area ----------- */
.wc-insurance-service-area {
  .wc-insurance-service-card {
    .wc-insurance-service-title {
      margin-bottom: 24px;
    }
    .wc-insurance-service-card-body {
      z-index: 1;
      position: relative;
      @include transition(0.3s);
    }
    .wc-insurance-service-link {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 2;
      padding: 25px 30px;
      background: var(--wc-primary);
      color: var(--bs-white);
      opacity: 0;
      visibility: hidden;
      @include transform(translateY(100%));
      @include transition(0.3s);
      .wc-insurance-service-link-text {
        font-size: 13px;
        text-transform: uppercase;
      }
      .wc-insurance-service-link-icon {
        height: 100%;
        width: 75px;
        color: var(--bs-dark);
        background: var(--wc-brand);
        position: absolute;
        right: 0;
      }
    }
    &:hover {
      .wc-insurance-service-card-body {
        @include transform(translateY(-50px));
      }
      .wc-insurance-service-link {
        opacity: 1;
        visibility: visible;
        @include transform(translateY(50px));
      }
    }
  }
}

/*------------- wc-insurance-industiry-area ----------- */
.wc-insurance-industiry-area {
  position: relative;
  background-size: cover;
  background-repeat: repeat-x;
  background-attachment: fixed;
  background-position: center center;
  background-image: url(./../images/bg/bg-man-with-phone.jpg);
  &::before {
    top: 0;
    right: 0;
    left: auto;
    z-index: 1;
    content: "";
    width: 650px;
    height: 100%;
    position: absolute;
    background: var(--wc-primary-alt) url(./../images/shape/shape-wave-line-alt.png);
  }
  &::after {
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(50, 75, 70, 0.5) url(./../images/shape/shape-dots-grid.png);
  }
  .wc-feature-item {
    padding: 30px 30px;
    background-color: var(--bs-body-bg);
    @include border-radius(6px);
    @include transition(0.3s);
    .wc-feature-icon {
      width: 100px;
      height: 100px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: var(--wc-brand);
      @include border-radius(50%);
      @include transition(0.3s);
      img {
        width: 50px;
        height: 50px;
        align-content: center;
        justify-content: center;
        position: relative;
        z-index: 2;
      }
    }
    &:hover {
      @include transform(translateY(-5px));
      .wc-feature-icon {
        background-color: var(--wc-brand-alt);
      }
    }
  }
}

/*------------- wc-branch-location-area ----------- */
.wc-branch-location-area {
  .wc-branch-location-row {
    .col:nth-child(1) {
      padding-top: 1px;
      border-right: 1px solid var(--bs-border-color);
      border-bottom: 1px solid var(--bs-border-color);
    }
    .col:nth-child(2) {
      border-bottom: 1px solid var(--bs-border-color);
    }
    .col:nth-child(3) {
      border-right: 1px solid var(--bs-border-color);
    }
    @include media-breakpoint-down(xxl) {
      .col:nth-child(1) {
        border-right: none;
      }
      .col:nth-child(3) {
        border-right: none;
        border-bottom: 1px solid var(--bs-border-color);
      }
    }
    @include media-breakpoint-down(xl) {
      .col:nth-child(1) {
        padding-top: 1px;
        border-right: 1px solid var(--bs-border-color);
        border-bottom: 1px solid var(--bs-border-color);
      }
      .col:nth-child(2) {
        border-bottom: 1px solid var(--bs-border-color);
      }
      .col:nth-child(3) {
        border-right: 1px solid var(--bs-border-color);
        border-bottom: none;
      }
    }
    @include media-breakpoint-down(lg) {
      .col:nth-child(1) {
        border-right: none;
      }
      .col:nth-child(3) {
        border-right: none;
        border-bottom: 1px solid var(--bs-border-color);
      }
    }
  }
}

/*------------- wc-wedo-area-03 ----------- */
.wc-wedo-area-03 {
  .wc-wedo-area-03-row {
    @include media-breakpoint-down(md) {
      .card {
        border-left: none !important;
      }
    }
  }
}

/*------------- wc-cta-area-03 ----------- */
.wc-cta-area-03 {
  position: relative;
  &::before {
    top: 0;
    left: 0;
    z-index: 0;
    content: "";
    width: 600px;
    height: 100%;
    position: absolute;
    background: var(--wc-primary-alt) url(./../images/shape/shape-wave-line-alt.png);
    @include media-breakpoint-down(xl) {
      width: 100%;
    }
  }
}
