@use "../utilities" as *;

/* theme buttons css */
.wc-btn {
  overflow: hidden;
  position: relative;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  text-align: center;
  line-height: normal;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  text-decoration: none;
  font-family: var(--wc-ff-body);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  padding: 10px 20px 10px 30px;
  @include border-radius(4px);
  @include transition(0.3s);
  @include border-radius(50px);

  .wc-btn-text,
  .wc-btn-icon {
    z-index: 2;
    position: relative;
  }
  .wc-btn-icon {
    line-height: 0;
    margin-left: 12px;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include border-radius(50px);
    @include transition(0.3s);
    i {
      @include transition(0.3s);
    }
  }

  &.wc-btn-sm {
    height: 50px;
    font-size: 13px;
    padding: 6px 12px 6px 18px;
  }

  &.wc-btn-lg {
    height: 80px;
    font-size: 16px;
    padding: 20px 60px 20px 90px;
  }

  &.wc-btn-icon {
    width: 60px;
    padding: unset;
    @include border-radius(50px);
    i {
      font-size: 18px;
      position: relative;
    }
    &.wc-btn-icon-sm {
      width: 50px;
      height: 50px;
      i {
        font-size: 16px;
        position: relative;
      }
    }
  }

  &.wc-btn-primary {
    color: var(--wc-white);
    background-color: var(--wc-primary);
    &:before {
      top: 0;
      left: 0;
      width: 0;
      height: 100%;
      content: "";
      position: absolute;
      background-color: var(--wc-brand-alt);
      @include transition(0.3s);
    }
    .wc-btn-icon {
      line-height: 0;
      background-color: var(--wc-primary-alt);
    }
  }

  &.wc-btn-brand {
    color: var(--wc-black);
    background-color: var(--wc-brand);
    &:before {
      top: 0;
      left: 0;
      width: 0;
      height: 100%;
      content: "";
      position: absolute;
      background-color: var(--wc-primary-alt);
      @include transition(0.3s);
    }
    .wc-btn-icon {
      line-height: 0;
      background-color: var(--wc-brand-alt);
    }
  }

  &.wc-btn-primary,
  &.wc-btn-brand {
    &:hover {
      color: var(--wc-white);
      &:before {
        width: 100%;
      }
      .wc-btn-icon {
        i {
          @include transform(rotate(45deg));
        }
      }
    }
  }

  &.wc-btn-light {
    color: var(--wc-primary);
    background-color: var(--wc-light);
    @include transition(0.3s);
    &:before {
      top: 0;
      left: 0;
      width: 0;
      height: 100%;
      content: "";
      position: absolute;
      background-color: var(--wc-primary-alt);
      @include transition(0.3s);
    }
    &:hover {
      color: var(--bs-white);
      &:before {
        width: 100%;
      }
      .wc-btn-icon {
        line-height: 0;
        background-color: var(--wc-brand-alt);
      }
    }
  }

  &.wc-btn-primary-alt {
    color: var(--wc-white);
    background-color: var(--wc-primary-alt);
    @include transition(0.3s);
    .wc-btn-icon {
      line-height: 0;
      i {
        @include transition(0.3s);
      }
    }
    &:hover {
      .wc-btn-icon {
        i {
          @include transform(rotate(45deg));
        }
      }
    }
  }

  &.wc-btn-brand-alt {
    color: var(--wc-dark);
    background-color: var(--wc-brand-alt);
    .wc-btn-icon {
      line-height: 0;
      i {
        @include transition(0.3s);
      }
    }
    &:hover {
      .wc-btn-icon {
        i {
          @include transform(rotate(45deg));
        }
      }
    }
  }
}
