@use "../utilities" as *;

/*------------- wc-blog-metro-area ------------ */
.wc-blog-metro-area {
  .blog-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    grid-auto-rows: 300px;
    grid-auto-flow: dense;
    padding: 20px;
    gap: 24px;
    @include media-breakpoint-down(md) {
      padding: 0;
      display: block;
    }
  }

  .blog-card {
    position: relative;
    display: flex;
    align-items: flex-end;
    overflow: hidden;
    cursor: pointer;
    @include media-breakpoint-down(md) {
      height: 300px;
      margin-bottom: 24px;
    }
    @include media-breakpoint-down(sm) {
      margin-bottom: 12px;
    }

    &::before {
      content: "";
      position: absolute;
      inset: 0;
      background-size: cover;
      background-position: center;
      background-image: inherit;
      transition: transform 0.6s ease;
      z-index: 0;
    }

    &[style]::before {
      background-image: attr(style url);
    }

    &.tall {
      grid-row: span 2;
    }

    &.wide {
      grid-column: span 2;
    }

    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: linear-gradient(0deg, rgba(0, 77, 56, 0.8) 0%, rgba(0, 77, 56, 0) 70%);
      transition: all 0.4s cubic-bezier(0.37, 0, 0.63, 1);
      z-index: 1;
    }

    .meta {
      position: relative;
      z-index: 2;
      padding: 24px;
      color: var(--bs-white);
      width: 100%;
      height: 100%;
      display: inline-flex;
      flex-direction: column;

      .category {
        display: inline-block;
        background: var(--bs-white);
        color: var(--bs-primary);
        font-size: 11px;
        text-transform: uppercase;
        font-weight: bold;
        padding: 4px 8px;
        border-radius: 3px;
        margin-bottom: auto;
        width: fit-content;
      }

      .date {
        display: block;
        font-size: 12px;
        text-transform: uppercase;
        opacity: 0.8;
        color: var(--bs-white);
        margin-bottom: 6px;
      }
    }

    // Hover effect
    &:hover {
      &::before {
        transform: scale(1.1);
      }

      .overlay {
        background: linear-gradient(0deg, rgba(0, 77, 56, 0.9) 0%, rgba(0, 77, 56, 0.1) 70%);
      }
    }
  }
}

/*------------- wc-blog-classic-area ------------ */
.wc-blog-classic-area {
  .card {
    .blog-read-more-link {
      opacity: 0;
      transition: all 0.4s ease;
      transform: translateX(-100%);
    }
    &:hover {
      .blog-read-more-link {
        opacity: 1;
        transform: translate(0);
      }
    }
  }
}

/*------------- wc-blog-masonry-area  ------------ */
.wc-blog-masonry-area {
  .masonry-item {
    .masonry-item-card {
      overflow: hidden;
      background-color: var(--bs-white);
      @include box-shadow(var(--wc-shadow-lg));
      @include transition-alt(all 0.3s ease);
      @include border-radius(8px);
    }
  }
}
