@use "../utilities" as *;

/*------------- wc-shop-area ------------ */
.wc-shop-products {
  .product-wrapper {
    overflow: hidden;
    position: relative;
    @include transition(0.35s);
    .product-img {
      padding: 80px 0;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: var(--wc-light);
      position: relative;
      img {
        @include transition(0.35s);
      }
      .product-badge {
        padding: 3px 10px;
        background-color: var(--bs-success);
        color: var(--bs-white);
        text-transform: uppercase;
        font-size: 9px;
        position: absolute;
        top: 30px;
        left: 30px;
        @include border-radius(3px);
      }
    }
    .product-action {
      width: 200px;
      padding: 20px 15px;
      line-height: 1;
      text-align: center;
      background-color: var(--bs-white);
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      position: absolute;
      bottom: 120px;
      left: 50%;
      opacity: 0;
      visibility: hidden;
      @include transform(translate(-50%, 150%));
      @include border-radius(50px);
      @include transition(0.5s);
      a {
        -webkit-transform: translateY(15px);
        transform: translateY(15px);
        opacity: 0;
      }
    }
    .product-meta {
      padding: 20px;
      text-align: center;
      background-color: var(--bs-white);
      position: relative;
      z-index: 2;
      .product-title {
        font-size: 18px;
      }
      .product-price {
        font-size: 14px;
        font-weight: 700;
        font-family: var(--wc-ff-number);
        .price-badge-x {
          color: var(--bs-danger);
          text-decoration: line-through;
        }
      }
    }
    &:hover {
      @include box-shadow(var(--wc-shadow-lg));
      .product-img {
        img {
          @include transform(scale(1.2));
        }
      }
      .product-action {
        opacity: 1;
        visibility: visible;
        @include transform(translate(-50%, 0));
        a {
          -webkit-transform: translateY(0);
          transform: translateY(0);
          -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
          opacity: 1;
          &:nth-child(2) {
            -webkit-transition-delay: 0.2s;
            transition-delay: 0.2s;
          }
          &:nth-child(3) {
            -webkit-transition-delay: 0.3s;
            transition-delay: 0.3s;
          }
          &:nth-child(4) {
            -webkit-transition-delay: 0.4s;
            transition-delay: 0.4s;
          }
        }
      }
    }
  }
}

/*------------- wc-shop-single-area ------------ */
.wc-shop-single-area {
  /* slider image */
  .product-image {
    .swiper-images {
      background-color: var(--wc-light);
      @include border-radius(8px);
      .swiper-slide {
        height: 600px;
        img {
          width: auto;
        }
      }
    }
    .swiper-thumbs {
      margin-top: 12px;
      .swiper-slide {
        opacity: 0.5;
        cursor: pointer;
        @include border-radius(4px);
        &.swiper-slide-thumb-active {
          opacity: 1;
          background-color: var(--wc-light);
        }
      }
    }
  }
}

