@use "../utilities" as *;

/* wc-instagram-feed */
.wc-instagram-feed {
  .wc-instagram-feed-image {
    position: relative;

    .wc-instagram-feed-icon {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      top: 0;
      left: 0;
      position: absolute;
      text-align: center;
      color: var(--bs-white);

      i {
        z-index: 1;
        position: relative;
        font-size: 20px;
        opacity: 0;
        visibility: hidden;
        @include transform(scale(0));
        @include transition(0.3s);
      }

      &::before {
        background: rgba(0, 90, 65, 0.5);
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
        opacity: 0;
        visibility: hidden;
        @include transform(scale(0));
        @include transition(0.3s);
        @include backdrop-filter(blur(4px));
      }
    }

    &:hover {
      .wc-instagram-feed-icon {
        i,
        &::before {
          opacity: 1;
          visibility: visible;
          @include transform(scale(1));
        }
      }
    }
  }
}
