/* counter */
.counter {
  gap: 6px;
  display: flex;
  align-items: flex-end;
  .odometer {
    font-size: 50px;
    font-weight: 700;
    line-height: 1.5;
    text-transform: uppercase;
    display: inline-block;
    color: var(--wc-primary);
    font-family: var(--wc-ff-number);
  }
  .counter-suffix {
    font-size: 32px;
    font-weight: 700;
    line-height: 2.5;
    color: var(--wc-dark);
  }
}
