@use "../utilities" as *;

/* bootstrap overwrite css */

// utilites
.container-fluid {
  @include media-breakpoint-up(sm) {
    --bs-gutter-x: 60px;
  }
  @include media-breakpoint-up(md) {
    --bs-gutter-x: 72px;
  }
  @include media-breakpoint-up(lg) {
    --bs-gutter-x: 54px;
  }
  @include media-breakpoint-up(xl) {
    --bs-gutter-x: 84px;
  }
  @include media-breakpoint-up(xxl) {
    --bs-gutter-x: 104px;
  }
}

// form
.form-control,
.form-select {
  font-size: 16px;
  font-weight: 300;
  padding: 16px 16px;
  border-color: var(--wc-border);
  &:focus,
  &:active {
    outline: 0;
    box-shadow: 0px 0px 0px 1px var(--wc-primary);
    border-color: var(--wc-primary);
  }
  &::placeholder {
    color: var(--bs-gray-500);
  }
}

.form-label {
  font-size: 13px;
  // font-weight: 500;
  // color: var(--bs-dark);
}

.form-check {
  position: relative;
  // checkbox
  .form-check-input[type="checkbox"] {
    top: -4px;
    left: 24px;
    opacity: 0;
    z-index: -1;
    width: 20px;
    height: 20px;
    position: absolute;
    appearance: none;
    & ~ .form-check-label {
      font-size: 14px;
      cursor: pointer;
      position: relative;
      vertical-align: top;
      text-transform: inherit;
      &::before {
        content: "";
        width: 16px;
        height: 16px;
        top: 4px;
        left: -25px;
        display: block;
        border-width: 2px;
        position: absolute;
        border-radius: 2px;
        border: 1px solid var(--bs-border-color);
      }
    }
    &:checked ~ .form-check-label {
      &::before {
        color: var(--bs-white);
        border-color: var(--bs-primary);
        background-color: var(--bs-primary);
      }
      &::after {
        top: 5px;
        left: -21px;
        font-size: 8px;
        color: var(--bs-white);
        content: "\e35f";
        position: absolute;
        font-family: uicons-regular-rounded !important;
      }
    }
  }

  // radio
  .form-check-input[type="radio"] {
    top: -4px;
    left: 24px;
    opacity: 0;
    z-index: -1;
    width: 20px;
    height: 20px;
    position: absolute;
    appearance: none;
    & ~ .form-check-label {
      left: 22px;
      font-size: 16px;
      font-weight: 500;
      margin-bottom: 0;
      cursor: pointer;
      position: relative;
      vertical-align: top;
      text-transform: inherit;
      &::before {
        content: "";
        width: 20px;
        height: 20px;
        top: 1px;
        left: -36px;
        display: block;
        border-width: 3px;
        position: absolute;
        border-radius: 3px;
        border: 1px solid var(--bs-border-color);
        background-color: var(--bs-gray-200);
        border-radius: 50%;
      }
      &.primary {
        &::before {
          border: 1px solid var(--bs-primary);
          background-color: var(--bs-primary);
        }
      }
      &.success {
        &::before {
          border: 1px solid var(--bs-success);
          background-color: var(--bs-success);
        }
      }
      &.warning {
        &::before {
          border: 1px solid var(--bs-warning);
          background-color: var(--bs-warning);
        }
      }
      &.danger {
        &::before {
          border: 1px solid var(--bs-danger);
          background-color: var(--bs-danger);
        }
      }
      &.dark {
        &::before {
          border: 1px solid var(--bs-dark);
          background-color: var(--bs-dark);
        }
      }
    }
    &:checked ~ .form-check-label {
      &::before {
        color: var(--bs-white);
        border-color: var(--bs-white);
        background-color: var(--bs-primary);
        box-shadow: 0 0 0 3px rgba(48, 86, 213, 0.15);
      }
      &::after {
        top: 3px;
        left: -30px;
        font-size: 10px;
        color: var(--bs-white);
        content: "\e35f";
        position: absolute;
        font-family: uicons-regular-rounded !important;
      }
    }
  }
}

// button
.btn-check:checked + .btn,
.btn.active,
.btn.show,
.btn:first-child:active,
:not(.btn-check) + .btn:active {
  border-color: var(--bs-border-color);
}

// card
.card {
  --bs-card-border-color: transparent;
  --bs-card-title-color: var(--bs-heading-color);
  .card-header,
  .card-footer {
    --bs-card-cap-padding-y: 1.5rem;
    --bs-card-cap-padding-x: 1.5rem;
    --bs-card-border-color: var(--bs-border-color);
    background-color: var(--bs-light);
  }
  .card-body {
    --bs-card-spacer-y: 2rem;
    --bs-card-spacer-x: 2rem;
    @include media-breakpoint-down(sm) {
      --bs-card-spacer-y: 1rem;
      --bs-card-spacer-x: 1rem;
    }
  }
}

// modal
.modal {
  cursor: url(./../images/icon/cursor-close.svg), auto;
  .modal-content {
    cursor: default !important;
    border: none;
  }
  &.show {
    .modal-dialog {
      transform: none !important;
    }
  }
  &.fade {
    .modal-dialog {
      transition: transform 0.3s ease-out;
      transform: matrix(0.95, 0, 0, 0.95, 0, 15);
    }
  }
  &.modal-static {
    .modal-dialog {
      transform: scale(1.02) !important;
    }
  }
}

// offcanvas
.offcanvas {
  .offcanvas-header,
  .offcanvas-footer,
  .offcanvas-body {
    --bs-offcanvas-padding-x: 1.5rem;
    --bs-offcanvas-padding-y: 1.5rem;
  }
}

// modal + offcanvas
.modal-backdrop,
.offcanvas-backdrop {
  cursor: url(./../images/icon/cursor-close.svg), auto;
  background-color: rgba(1, 90, 65, 0.5);
  @include backdrop-filter(blur(10px));
  &.fade {
    opacity: 0;
  }
  &.show {
    opacity: 1;
  }
}

// list-group
.list-group-item {
  --bs-list-group-item-padding-y: 1rem;
  --bs-list-group-item-padding-x: 1.5rem;
}

// table
.table {
  --bs-table-color: var(--bs-body-color);
 > :not(caption)> *> * {
    padding: 1rem 1.5rem;
  }
}
