@use "../utilities" as *;

/* footer css */
.wc-footer-area {
  font-size: 16px;
  overflow: hidden;
  position: relative;
  color: var(--bs-gray-700);
  background-color: var(--wc-light-alt);
  background-image: url(./../images/shape/shape-border-grid.png);
  background-size: auto;
  background-repeat: repeat;
  background-position: center center;
  background-blend-mode: multiply;
  // wc-footer-top
  .wc-footer-top {
    z-index: 3;
    position: relative;
    &::before,
    &::after {
      left: 0;
      right: 0;
      content: "";
      width: 100%;
      height: 50%;
      z-index: -1;
      position: absolute;
    }
    &::before {
      top: 0;
      background-color: var(--wc-white);
    }
    &::after {
      bottom: 0;
      background-color: transparent;
    }
    .container {
      .row {
        top: 0;
        position: relative;
        padding: 16px 16px;
        background-color: var(--wc-primary);
        @include border-radius(12px);
        @include media-breakpoint-up(sm) {
          padding: 32px 32px;
        }
        @include media-breakpoint-up(md) {
          padding: 60px 60px;
        }
        &::before,
        &::after {
          top: 0;
          right: 0;
          content: "";
          width: 250px;
          height: 100%;
          position: absolute;
          background-color: rgba(0, 0, 0, 0.05);
        }
        &::before {
          clip-path: polygon(100% 0, 0 0, 100% 100%);
        }
        &::after {
          clip-path: polygon(100% 0, 0% 100%, 100% 100%);
        }
      }
    }
  }
  // wc-footer-main
  .wc-footer-main {
    padding-top: 40px;
    padding-bottom: 40px;
    @include media-breakpoint-up(md) {
      padding-top: 60px;
      padding-bottom: 60px;
    }
    @include media-breakpoint-up(xl) {
      padding-top: 80px;
      padding-bottom: 80px;
    }
    @include media-breakpoint-up(xxl) {
      padding-top: 120px;
      padding-bottom: 120px;
    }
    .wc-footer-solial-link {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--wc-dark);
      background-color: var(--wc-light);
      @include border-radius(4px);
      @include transition(0.3s);
      &:hover {
        color: var(--wc-white);
        background-color: var(--wc-primary-alt);
      }
    }
    .wc-footer-title {
      font-size: 16px;
      font-weight: 800;
      letter-spacing: 0.5px;
      text-transform: uppercase;
      color: var(--wc-dark);
      margin-bottom: 30px;
      @include media-breakpoint-down(lg) {
        margin-bottom: 15px;
      }
    }
    ul {
      li {
        &:not(:last-child) {
          margin-bottom: 15px;
        }
        a {
          font-size: 16px;
          font-weight: 500;
          color: var(--bs-gray-700);
          display: inline-block;
          position: relative;
          &::after {
            left: 0;
            bottom: 0;
            width: 0%;
            height: 1px;
            content: "";
            position: absolute;
            background-color: currentColor;
            @include transition(0.45s);
          }
          &:hover {
            color: var(--wc-primary-alt);
            &::after {
              width: 100%;
            }
          }
        }
      }
    }
  }
  // wc-footer-bottom
  .wc-footer-bottom {
    padding: 32px 0;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    background-color: var(--wc-light);
    border-top: 1px solid var(--wc-border);
    position: relative;
    z-index: 1;
  }
}
