@use "../utilities" as *;

/* Define mixin for avatar styles */
@mixin avatar-style {
  width: 60px;
  height: 60px;
  font-weight: 700;
  cursor: pointer;
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: var(--wc-dark);
  background-color: var(--wc-light);
  border: 3px solid var(--bs-tertiary-bg);
  @include border-radius(50%);
}

/* avatars */
.avatar-item {
  @include avatar-style;

  img {
    height: auto;
    max-width: 100%;
    @include border-radius(50%);
  }
}

.avatar-group {
  display: flex;
  justify-content: flex-start;
  .avatar-item {
    @include avatar-style;
    margin-right: -25px;
    transition: transform 0.2s ease-in-out;

    &:hover {
      z-index: 1;
      transform: scale(1.1);
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &.avatar-group-md {
    .avatar-item {
      width: 45px;
      height: 45px;
      margin-right: -15px;
    }
  }
}
