@use "../utilities" as *;

/* marquee-slider */
.marquee-slider {
  gap: 24px;
  display: flex;
  align-items: center;
  user-select: none;
  will-change: transform;

  &.marquee-hover-paused:hover {
    animation-play-state: paused;
  }

  .marquee-group {
    gap: 24px;
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: space-around;
    min-width: 100%;
    padding: 12px 0;

    /* bg-dot-style */
    &.bg-dot-style {
      .marquee-item {
        gap: 48px;
        display: flex;
        flex-shrink: 0;
        align-items: center;
        justify-content: space-between;
        padding: 24px 12px;
        cursor: pointer;
        .marquee-icon {
          width: 8px;
          height: 8px;
          background-color: var(--wc-brand);
          @include border-radius(50%);
        }
        .marquee-text {
          font-size: 50px;
          text-transform: uppercase;
          font-weight: 900;
          line-height: inherit;
          color: var(--wc-brand);
        }
        &:nth-child(odd) {
          .marquee-text {
            mix-blend-mode: normal;
            display: inline-block;
            color: transparent;
            font-family: var(--wc-ff-body);
            stroke-width: 1px;
            -webkit-text-stroke-width: 1px;
            -webkit-text-stroke-color: var(--wc-brand);
          }
        }
      }
    }

    /* bg-variable-style */
    &.bg-variable-style {
      .marquee-item {
        gap: 12px;
        display: flex;
        flex-shrink: 0;
        align-items: center;
        justify-content: space-between;
        padding: 24px 24px;
        cursor: pointer;

        .marquee-icon img {
          width: 50px;
          height: 50px;
        }

        .marquee-text {
          font-size: 36px;
          font-weight: 700;
          text-transform: uppercase;
          color: var(--wc-primary);
        }
      }
    }

    /* feature-box-style */
    &.feature-box-style {
      .marquee-item {
        gap: 16px;
        display: flex;
        flex-shrink: 0;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        padding: 24px 32px;
        background-color: var(--bs-body-bg);
        @include box-shadow(var(--wc-shadow-modern));
        @include border-radius(4px);
        @include transition(0.3s);
        &:hover {
          @include box-shadow(var(--wc-shadow-sm));
        }
        i {
          font-size: 18px;
          line-height: normal;
          color: var(--bs-dark-text-emphasis);
        }
        span {
          font-weight: 700;
          color: var(--bs-dark-text-emphasis);
        }
      }
    }
  }
}

/* marquee-right-to-left */
.marquee-right-to-left {
  &.marquee-slow {
    animation: marquee-right-to-left 60s linear infinite;
    @include media-breakpoint-down(lg) {
      animation-duration: 25s;
    }
    @include media-breakpoint-down(md) {
      animation-duration: 15s;
    }
  }
  &.marquee-normal {
    animation: marquee-right-to-left 40s linear infinite;
    @include media-breakpoint-down(lg) {
      animation-duration: 25s;
    }
    @include media-breakpoint-down(md) {
      animation-duration: 15s;
    }
  }
  &.marquee-fast {
    animation: marquee-right-to-left 20s linear infinite;
  }
  @keyframes marquee-right-to-left {
    from {
      transform: translateX(0);
    }
    to {
      transform: translateX(-100%);
    }
  }
}

/* marquee-left-to-right */
.marquee-left-to-right {
  &.marquee-slow {
    animation: marquee-left-to-right 60s linear infinite;
    @include media-breakpoint-down(lg) {
      animation-duration: 25s;
    }
    @include media-breakpoint-down(md) {
      animation-duration: 15s;
    }
  }
  &.marquee-normal {
    animation: marquee-left-to-right 40s linear infinite;
    @include media-breakpoint-down(lg) {
      animation-duration: 25s;
    }
    @include media-breakpoint-down(md) {
      animation-duration: 15s;
    }
  }
  &.marquee-fast {
    animation: marquee-left-to-right 20s linear infinite;
  }
  @keyframes marquee-left-to-right {
    from {
      transform: translateX(-100%);
    }
    to {
      transform: translateX(0);
    }
  }
}
