@use "../utilities" as *;

/* wc-creative-tabs */
.wc-creative-tabs,
.wc-creative-tabs-alt {
  &.nav-tabs {
    justify-content: center;
    .nav-item {
      padding: 0;
      background-color: transparent;
      @include media-breakpoint-down(sm) {
        width: 100%;
      }
      .nav-link {
        height: 100%;
        text-align: left;
        padding: 30px 30px;
        border: 0;
        margin-bottom: 0;
        background-color: transparent;
        position: relative;
        font-size: 16px;
        font-weight: 700;
        text-transform: uppercase;
        color: var(--wc-dark);
        &::before {
          width: calc(100% + 2px);
          height: 3px;
          left: -1px;
          top: 0;
          content: "";
          display: block;
          position: absolute;
          transform: scaleX(0);
          background: var(--bs-dark);
          @include transition(0.3s);
        }
        &.active {
          color: var(--wc-primary);
          // background-color: var(--wc-brand-light);
          &::before {
            background: var(--bs-primary);
            @include transform(scaleX(1));
          }
        }
      }
    }
  }
}

/* wc-creative-tabs */
.wc-creative-tabs {
  &.nav-tabs {
    border-top: 1px solid var(--wc-border);
    border-bottom: 1px solid var(--wc-border);
    .nav-item {
      border-right: 1px solid var(--wc-border);
      @include media-breakpoint-down(sm) {
        border-right: none;
        &:nth-child(1) {
          border-bottom: 1px solid var(--wc-border);
        }
        &:nth-child(2) {
          border-bottom: 1px solid var(--wc-border);
        }
      }
      &:last-child {
        border-right: 0;
      }
    }
  }
}
/* wc-creative-tabs-alt */
.wc-creative-tabs-alt {
  &.nav-tabs {
    border-top: 1px solid var(--wc-border-alt-2);
    border-bottom: 1px solid var(--wc-border-alt-2);
    .nav-item {
      border-right: 1px solid var(--wc-border-alt-2);
      @include media-breakpoint-down(sm) {
        border-right: none;
        &:nth-child(1) {
          border-bottom: 1px solid var(--wc-border-alt-2);
        }
        &:nth-child(2) {
          border-bottom: 1px solid var(--wc-border-alt-2);
        }
      }
      &:last-child {
        border-right: 0;
      }
    }
  }
}
