@use "../utilities" as *;

/*------------- wc-hero-area-five ------------ */
.wc-hero-area-five {
  .scale-slider {
    figure {
      opacity: 0;
      @include transform(scale(1));
      @include transition-alt(opacity 1s ease, transform 5s ease);
      z-index: 0;
      &.active {
        opacity: 1;
        z-index: 1;
        @include transform(scale(1.1));
      }
    }
  }

  .slider-indicators {
    .dot {
      width: 40px;
      height: 6px;
      opacity: 0.5;
      cursor: pointer;
      background-color: var(--bs-white);
      @include transition-alt(opacity 0.3s ease);
      @include border-radius(50px);
      &.active {
        opacity: 1;
      }
    }
  }

  .wc-hero-right-card {
    position: relative;
    padding-left: 120px;
    padding-right: 120px;
    @include media-breakpoint-between(xxl, "3xl") {
      padding-left: 60px;
      padding-right: 60px;
    }
    @include media-breakpoint-down("3xl") {
      padding-left: 30px;
      padding-right: 30px;
    }
    @include media-breakpoint-down(xl) {
      padding-left: 0px;
      padding-right: 0px;
    }
    &::before {
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      background: url(./../images/shape/shape-church-noise.png);
      background-repeat: repeat;
    }
  }

  .typing-container {
    white-space: nowrap;
    width: fit-content;
    border-right: 3px solid var(--bs-primary);
    @include animation(blink 1s step-end infinite);
    @keyframes blink {
      0%,
      100% {
        border-color: transparent;
      }
      50% {
        border-color: var(--bs-primary);
      }
    }
  }

  .wc-hero-follow-us {
    gap: 12px;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 0;
    top: 56%;
    position: absolute;
    white-space: nowrap;
    writing-mode: sideways-lr;
    @include transform(translateY(-50%));
    @include media-breakpoint-down(xxl) {
      right: 60px;
    }
    @include media-breakpoint-down(xl) {
      right: 40px;
    }
    @include media-breakpoint-down(sm) {
      right: 20px;
    }
    .wc-hero-follow-text {
      font-size: 13px;
      font-weight: 700;
      text-transform: uppercase;
      color: var(--bs-primary);
      @include media-breakpoint-down(xxl) {
        color: var(--bs-white);
      }
    }

    .wc-hero-follow-line {
      width: 1px;
      height: 25px;
      background: var(--bs-primary);
      display: flex;
      align-items: center;
      justify-content: center;
      @include media-breakpoint-down(xxl) {
        background: var(--bs-white);
      }
    }

    .wc-hero-follow-link {
      a {
        line-height: 0;
        padding: 12px 15px;
        color: var(--bs-primary);
        display: inline-block;
        @include media-breakpoint-down(xxl) {
          color: var(--bs-white);
        }
      }
    }
  }
  .wc-typing-breck {
    @include media-breakpoint-up("3xl") {
      display: none;
    }
    @include media-breakpoint-between(xl, xxl) {
      display: block;
    }
    @include media-breakpoint-between(sm, xl) {
      display: none;
    }
  }
}

/*------------- wc-service-area-05 ------------ */
.wc-service-area-05 {
  .card {
    overflow: hidden;
    &::before {
      width: 30px;
      height: 30px;
      content: "";
      bottom: 0;
      left: 0;
      position: absolute;
      background-color: var(--wc-light);
      @include transition(0.3s);
    }
    &:hover {
      &::before {
        width: 100%;
        height: 100%;
      }
      .wc-sevice-card-shape {
        filter: brightness(2);
      }
    }
  }
}

/*------------- wc-call-to-action-area ------------ */
.wc-call-to-action-area {
  &.bg-cta-alt {
    .wc-call-to-action-top {
      background-image: url(../images/bg/bg-man-with-phone.jpg);
    }
    @include media-breakpoint-down(xl) {
      .wc-call-to-action-bottom {
        .card {
          margin-top: -176px;
        }
      }
    }
    @include media-breakpoint-down(xxl) {
      .wc-call-to-action-bottom {
        .card {
          margin-top: -178px;
        }
      }
    }
    @include media-breakpoint-down(md) {
      .wc-call-to-action-bottom {
        .card {
          margin-top: -173px;
        }
      }
    }
    @include media-breakpoint-down(sm) {
      .wc-call-to-action-bottom {
        .card {
          margin-top: -140px;
        }
      }
    }
  }
}

/*------------- wc-word-process-area-03 ------------ */
.wc-word-process-area-03 {
  .wc-process-timeline {
    padding: 0;
    list-style: none;
    position: relative;
    &::before {
      left: 40px;
      content: "";
      height: 100%;
      width: 1px;
      position: absolute;
      background: var(--bs-border-color);
    }
    .wc-process-item {
      gap: 24px;
      display: flex;
      align-items: center;
      margin-bottom: 40px;
      &:hover {
        .wc-process-step {
          @include transform(scale(1.1));
          background-color: var(--bs-primary);
          color: var(--bs-white);
          -webkit-text-stroke-color: var(--bs-white);
        }
      }
      .wc-process-step {
        width: 80px;
        height: 80px;
        display: flex;
        flex-shrink: 0;
        align-items: center;
        justify-content: center;
        font-size: 32px;
        font-weight: 700;
        font-family: var(--ff-number);
        background-color: var(--wc-light-alt);
        position: relative;
        z-index: 3;
        @include border-radius(50%);
        @include transition(0.3s);
      }
      .wc-process-content {
        p {
          font-size: 16px;
        }
      }
    }
  }
}

/*-------------  wc-composition-area ------------ */
.wc-composition-area {
  position: relative;
  &::before {
    background-size: auto;
    background-repeat: no-repeat;
    background-position: center center;
    background-blend-mode: multiply;
    background-image: url(../images/shape/shape-map-dots-alt.png);
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
  }
}
