@use "../utilities" as *;

/* progress-bar */
.circle-progress-bar {
  position: relative;
  .circle-progress-count {
    left: 0;
    top: 50%;
    right: 0;
    width: 110px;
    height: 110px;
    margin: 0 auto;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    font-weight: 700;
    color: var(--bs-dark);
    background-color: var(--bs-white);
    font-family: var(--wc-ff-number);
    @include transform(translateY(-50%));
    @include box-shadow(var(--wc-shadow-lg));
    @include border-radius(50%);
    i {
      font-size: 16px;
      margin-left: 2px;
      color: var(--bs-body-color);
      vertical-align: baseline;
    }
  }
}
