@use "../utilities" as *;

/*------------- wc-testimonials-top-area ------------ */
.wc-testimonials-top-area {
  overflow: hidden;
  position: relative;
  background: linear-gradient(to top, var(--wc-light), transparent);
  .wc-slideway-review {
    top: 0;
    left: 0;
    width: 100px;
    height: 100%;
    padding: 30px 30px;
    position: absolute;
    white-space: nowrap;
    writing-mode: sideways-lr;
    background-color: var(--wc-white);
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

/*------------- wc-testimonials-area-01 ------------ */
.wc-testimonials-area-01 {
  .testimonials-stroke-text {
    left: 0;
    top: 50%;
    height: 100%;
    display: block;
    position: absolute;
    white-space: nowrap;
    writing-mode: sideways-lr;
    background-color: var(--wc-primary);
    @include transform(translateY(-50%));
    @include media-breakpoint-down("3xl") {
      display: none;
    }
    .text-stroke {
      margin: 0 0;
      font-size: 100px;
      text-transform: uppercase;
      opacity: 0.1;
    }
  }
}

/*------------- wc-testimonials-area-02 ------------ */
.wc-testimonials-area-02 {
  width: 100%;
  height: 100%;
  min-height: 600px;
  background: rgb(0, 77, 56);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  &::after {
    top: 0;
    right: 0;
    z-index: 1;
    content: "";
    width: 250px;
    height: 100%;
    position: absolute;
    background: var(--wc-primary) url(./../images/shape/shape-wave-line-alt.png);
  }
  .wc-testimonials-swiper-02 {
    margin-right: -54vw;
    @include media-breakpoint-down("3xl") {
      margin-right: -70vw;
    }
    @include media-breakpoint-down(xxl) {
      margin-right: -50vw;
    }
    @include media-breakpoint-down(lg) {
      margin-right: -60vw;
    }
    @include media-breakpoint-down(md) {
      margin-right: 0;
    }
  }
}

/*------------- wc-testimonials-area-03 ----------- */
.wc-testimonials-area-03 {
  .masonry-item-card {
    padding: 25px 25px;
    background-color: var(--wc-light);
    @include border-radius(4px);
    @include transition(0.3s);
    &:hover {
      background-color: var(--bs-white);
      @include transform(translateY(-5px));
      @include box-shadow(var(--wc-shadow-lg));
    }
  }
}

/*------------- wc-testimonials-area-04 ----------- */
.wc-testimonials-area-04 {
  position: relative;
  &::after {
    top: 0;
    right: 0;
    left: auto;
    z-index: 0;
    content: "";
    width: 550px;
    height: 100%;
    position: absolute;
    background: var(--wc-primary-alt) url(./../images/shape/shape-wave-line-alt.png);
    @include media-breakpoint-between(md, "3xl") {
      width: 25%;
    }
    @include media-breakpoint-down(md) {
      display: none;
    }
  }
}
