@use "../utilities" as *;

/* common css */
.common-section {
  overflow: hidden;
  position: relative;
  padding-top: 40px;
  padding-bottom: 40px;
  @include media-breakpoint-up(md) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  @include media-breakpoint-up(xl) {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  @include media-breakpoint-up(xxl) {
    padding-top: 120px;
    padding-bottom: 120px;
  }
}

.common-space {
  overflow: hidden;
  margin-top: 40px;
  @include media-breakpoint-up(md) {
    margin-top: 60px;
  }
  @include media-breakpoint-up(xl) {
    margin-top: 80px;
  }
  @include media-breakpoint-up(xxl) {
    margin-top: 120px;
  }
}

.hero-title {
  font-size: 40px;
  font-weight: 800;
  @include media-breakpoint-up(sm) {
    font-size: 50px;
  }
  @include media-breakpoint-up(md) {
    font-size: 60px;
  }
  @include media-breakpoint-up(lg) {
    font-size: 70px;
  }
  &:hover {
    .text-highlight-bg {
      bottom: 0px;
      height: 85%;
    }
  }
}

.section-header {
  margin-bottom: 40px;
  @include media-breakpoint-up(xl) {
    margin-bottom: 60px;
  }
  @include media-breakpoint-up(xxl) {
    margin-bottom: 80px;
  }
}

.section-title,
.section-title-alt {
  font-size: 30px;
  font-weight: 800;
  @include media-breakpoint-up(md) {
    font-size: 40px;
  }
  @include media-breakpoint-up(lg) {
    font-size: 50px;
  }
  &:hover {
    .text-highlight-bg {
      bottom: 0px;
      height: 85%;
    }
  }
}

.section-title {
  color: var(--wc-dark);
}

.section-title-alt {
  color: var(--wc-white);
}

.section-sub-title,
.section-sub-title-alt,
.section-sub-title-alt-02 {
  font-size: 13px;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 1px;
  text-transform: uppercase;
  padding: 2px 16px;
  margin-bottom: 16px;
  display: inline-block;
  @include border-radius(50px);
}

.section-sub-title {
  color: var(--wc-primary);
  background-color: var(--wc-light);
}

.section-sub-title-alt {
  color: var(--wc-primary);
  background: rgba(var(--wc-primary-rgb), 0.1);
  @include backdrop-filter(blur(3px));
}

.section-sub-title-alt-02 {
  color: var(--wc-white);
  background: rgba(var(--wc-primary-rgb), 0.5);
  @include backdrop-filter(blur(3px));
}

// common-pagination
.common-pagination {
  gap: 8px;
  font-weight: 700;
  margin-bottom: 0px;
  .page-item {
    gap: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    .page-link {
      box-shadow: none;
      background: 0 0;
      color: var(--bs-body-color);
      width: 45px;
      height: 45px;
      display: flex;
      align-items: center;
      justify-content: center;
      line-height: normal;
      font-size: 16px;
      border: none;
      @include border-radius(50%);
      @include transition(0.3s);
      &:hover {
        color: var(--wc-primary);
        @include box-shadow(var(--wc-shadow-modern));
      }
      i {
        font-size: 18px;
        line-height: 0;
        margin-top: 6px;
      }
    }
    &.active {
      .page-link {
        color: var(--bs-white);
        background-color: var(--wc-primary);
        border-color: var(--wc-primary);
      }
    }
  }
}

// link-hover-underline
.link-hover-underline {
  position: relative;
  padding-bottom: 2px;

  &::after {
    left: 0;
    bottom: 0;
    width: 0%;
    height: 1px;
    content: "";
    position: absolute;
    background-color: currentColor;
    @include transition(0.45s);
  }

  &:hover::after {
    width: 100%;
  }
}

// line-clamp
.line-clamp {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;

  &.line-clamp-1 {
    line-clamp: 1;
    -webkit-line-clamp: 1;
  }

  &.line-clamp-2 {
    line-clamp: 2;
    -webkit-line-clamp: 2;
  }

  &.line-clamp-3 {
    line-clamp: 3;
    -webkit-line-clamp: 3;
  }

  &.line-clamp-4 {
    line-clamp: 4;
    -webkit-line-clamp: 4;
  }

  &.line-clamp-5 {
    line-clamp: 5;
    -webkit-line-clamp: 5;
  }
}

// text-highlight
.text-highlight {
  font-weight: 700;
  font-family: var(--wc-ff-body);
  position: relative;
  display: inline-block;

  .text-highlight-bg {
    left: 0px;
    right: 0px;
    bottom: 5px;
    width: 100%;
    height: 10px;
    position: absolute;
    display: inline-block;
    background-image: linear-gradient(120deg, rgb(1, 89, 65, 0.35) 0%, rgb(236, 235, 65, 0.35) 100%);
    @include transition(0.3s);
  }
}

// title-highlight
.title-highlight {
  font-weight: 700;
  background: var(--wc-brand);
  color: var(--wc-primary);
  padding: 0 12px;
  display: inline-flex;
  border: 1px solid var(--wc-brand);
  position: relative;

  &::before,
  &::after {
    position: absolute;
    content: "";
    width: 12px;
    height: 12px;
    background: var(--wc-brand);
    @include media-breakpoint-down(sm) {
      width: 8px;
      height: 8px;
    }
  }

  &::before {
    left: -7px;
    top: -5px;
    @include media-breakpoint-down(sm) {
      left: -5px;
      top: -3px;
    }
  }
  &::after {
    right: -7px;
    top: -5px;
    @include media-breakpoint-down(sm) {
      right: -5px;
      top: -3px;
    }
  }

  span.corner {
    position: absolute;
    width: 12px;
    height: 12px;
    background: var(--wc-brand);
    @include media-breakpoint-down(sm) {
      width: 8px;
      height: 8px;
    }
  }

  .corner.bl {
    left: -7px;
    bottom: -5px;
    @include media-breakpoint-down(sm) {
      left: -5px;
      bottom: -3px;
    }
  }

  .corner.br {
    right: -7px;
    bottom: -5px;
    @include media-breakpoint-down(sm) {
      right: -5px;
      bottom: -3px;
    }
  }
}

// text-stroke
.text-stroke {
  line-height: inherit;
  mix-blend-mode: normal;
  display: inline-block;
  color: transparent;
  font-family: var(--wc-ff-misc);
  stroke-width: 0.5px;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: var(--bs-body-color);

  &.text-stroke-light {
    -webkit-text-stroke-color: var(--wc-light);
  }

  &.text-stroke-muted {
    -webkit-text-stroke-color: var(--bs-gray-300);
  }

  &.text-stroke-dark {
    -webkit-text-stroke-color: var(--bs-dark);
  }

  &.text-stroke-primary {
    -webkit-text-stroke-color: var(--bs-primary);
  }
}

// bg-gradient
.bg-gradient-to-top {
  background: var(--wc-bg-gradient-to-top);
}
.bg-gradient-to-bottom {
  background: var(--wc-bg-gradient-to-bottom);
}

// bg-filter
.bg-filter {
  position: relative;

  &::before {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    content: "";
    position: absolute;
    @include transition(0.3s);
    @include backdrop-filter(blur(8px));
  }

  &.filter-light {
    &::before {
      background-color: rgba(0, 0, 0, 0.05);
    }
  }

  &.filter-primary {
    &::before {
      background-color: rgba(10, 30, 25, 0.25);
    }
  }

  &.filter-brand {
    &::before {
      background-color: rgba(0, 0, 0, 0.6);
    }
  }
}

// transform-item-hover
.transform-item-hover {
  // overflow: hidden;
  backface-visibility: hidden;

  .transform-item {
    flex-shrink: 0;
    fill: currentcolor;
    @include transition(0.2s);
  }

  .transform-item-scale,
  .transform-item-scaleX {
    @include transition(0.3s);
  }

  .image-transform-effect {
    img {
      @include transition(0.5s);

      &:first-child {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: 0;
        position: absolute;
        @include filter(blur(10px));
        @include transform(translateX(50%) scaleX(2));
        @include transition(0.4s);
      }
    }
  }

  &:hover {
    .transform-item {
      @include transform(translate3d(0.25em, 0, 0));
    }

    .transform-item-scale {
      @include transform(scale(1.05));
    }

    .transform-item-scaleX {
      @include transform(scaleX(-1));
    }

    .image-transform-effect {
      img {
        &:first-child {
          opacity: 1;
          @include filter(blur(0));
          @include transform(translateX(0) scaleX(1));
        }

        &:nth-child(2) {
          opacity: 0;
          @include filter(blur(10px));
          @include transform(translateX(-50%) scaleX(2));
        }
      }
    }
  }
}

// wc-blur-shape
.bg-blur-shape-01,
.bg-blur-shape-02,
.bg-blur-shape-03,
.bg-blur-shape-04 {
  width: 500px;
  height: 500px;
  position: absolute;
  background: rgba(0, 77, 56, 0.3);
  @include filter(blur(120px));
  @include border-radius(50%);
}

.bg-blur-shape-01 {
  bottom: -200px;
  left: -250px;
}

.bg-blur-shape-02 {
  bottom: -200px;
  right: -250px;
}

.bg-blur-shape-03 {
  top: -200px;
  left: -250px;
}

.bg-blur-shape-04 {
  top: -200px;
  right: -250px;
}

// image-reveal-effect
.image-reveal-effect {
  position: relative;
  overflow: hidden;

  &:after {
    z-index: 1;
    content: "";
    position: absolute;
    width: 200%;
    height: 0%;
    left: 50%;
    top: 50%;
    background-color: rgba(255, 255, 255, 0.3);
    @include transform(translate(-50%, -50%) rotate(-45deg));
  }

  &:hover {
    &:after {
      height: 250%;
      background-color: transparent;
      @include transition(2s);
    }
  }
}

// animation-circle
.animation-circle {
  animation: circle 15s linear infinite;
  @keyframes circle {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}

// animation-top-to-bottom
.animation-top-to-bottom {
  animation: top-to-bottom 7s linear infinite;
  @keyframes top-to-bottom {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(30px);
    }
    100% {
      transform: translateY(0);
    }
  }
}
// animation-bottom-to-top
.animation-bottom-to-top {
  animation: bottom-to-top 7s linear infinite;
  @keyframes bottom-to-top {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-30px);
    }
    100% {
      transform: translateY(0);
    }
  }
}

// animation-lef-to-right
.animation-left-to-right {
  animation: left-to-right 7s linear infinite;
  @keyframes left-to-right {
    0% {
      transform: translateX(0);
    }
    50% {
      transform: translateX(30px);
    }
    100% {
      transform: translateX(0);
    }
  }
}

// wc-grid-list
.wc-grid-list {
  gap: 12px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-left: 18px;
  @include media-breakpoint-down(md) {
    grid-template-columns: 1fr;
  }
}

.wc-opacity-overlay-to-top {
  bottom: 0;
  z-index: 3;
  width: 100%;
  position: absolute;
  background: linear-gradient(to top, #ecf2f2, #ecf2f2, transparent);
  padding-top: 40px;
  padding-bottom: 40px;
  @include media-breakpoint-up(md) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  @include media-breakpoint-up(xl) {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  @include media-breakpoint-up(xxl) {
    padding-top: 120px;
    padding-bottom: 120px;
  }
}

.figure-arrow {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    left: 12px;
    top: 140%;
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 8px solid #fff;
    clear: both;
    z-index: 1;
  }
}

// wc-play-btn
.wc-play-btn {
  width: 120px;
  height: 120px;
  color: var(--wc-dark);
  background-color: var(--wc-brand);
  display: flex;
  align-items: center;
  justify-content: center;
  @include border-radius(50%);
}
.pulse-effect {
  isolation: isolate;
  transition: all 0.25s cubic-bezier(0, 0, 0.31, 0.99);
  animation: pulse-effect 2s infinite;
  @keyframes pulse-effect {
    0% {
      box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.5);
    }
    70% {
      box-shadow: 0 0 0 25px rgba(255, 255, 255, 0);
    }
    100% {
      box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
    }
  }
}

.spy-scroll-nav {
  .nav-link {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 24px;
    @include transition(0.3s);
    @include border-radius(4px);
    .spy-scroll-nav-text {
      font-weight: 700;
      color: var(--bs-dark);
      @include transition(0.3s);
    }
    .spy-scroll-nav-icon {
      color: var(--bs-primary);
      line-height: 0;
      opacity: 0;
      visibility: hidden;
      @include transform(translateX(-20px));
      @include transition(0.3s);
    }
    &:hover,
    &.active {
      background-color: var(--bs-body-bg);
      .spy-scroll-nav-text {
        color: var(--bs-primary);
      }
      .spy-scroll-nav-icon {
        opacity: 1;
        visibility: visible;
        @include transform(translateX(0));
      }
    }
  }
}

.moving-particles-primary {
  background-color: var(--bs-primary);
}

/*------------- wc-work-process-area ------------ */
.wc-work-process-area {
  background-repeat: repeat;
  background-image: url(./../images/shape/shape-aper-grid.jpg);

  .wc-process-step-wrapper {
    .process-step-item {
      position: relative;
      padding: 50px 30px;
      background-color: var(--wc-white);
      @include transition(0.3s);
      @include border-radius(6px);
      @include box-shadow(var(--wc-shadow-modern));

      .process-step-image-01,
      .process-step-image-02,
      .process-step-image-03,
      .process-step-image-04 {
        width: 150px;
        height: 150px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 40px;
        margin-left: auto;
        margin-right: auto;
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;
        border: 10px solid var(--wc-light);
        @include transition(0.3s);
        @include border-radius(50%);
      }

      .process-step-image-01 {
        background-image: url(./../images/banner/sm-01.jpg);
      }

      .process-step-image-02 {
        background-image: url(./../images/banner/sm-02.jpg);
      }

      .process-step-image-03 {
        background-image: url(./../images/banner/sm-03.jpg);
      }

      .process-step-image-04 {
        background-image: url(./../images/banner/sm-04.jpg);
      }

      .process-step-number-alt {
        width: 35px;
        height: 35px;
        font-size: 13px;
        font-weight: 700;
        color: var(--wc-primary);
        background-color: var(--wc-brand);
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 10px;
        left: -12px;
        @include border-radius(50%);
        @include transition(0.3s);
      }

      .process-step-number-wrap {
        position: relative;
        margin-bottom: 30px;
        .process-step-number {
          z-index: 1;
          position: relative;
          padding: 8px 24px;
          display: inline-block;
          font-size: 14px;
          font-weight: 700;
          text-transform: uppercase;
          font-family: var(--wc-ff-number);
          color: var(--wc-primary);
          background-color: var(--wc-light);
          @include transition(0.3s);
          @include border-radius(4px);
        }

        .process-step-line {
          top: 20px;
          position: absolute;
          border-top: 1px dashed var(--bs-border-color);
          width: calc(100% - 60px);
          @include transform(translateX(-12%));
          @include media-breakpoint-up(md) {
            @include transform(translateX(-65%));
          }
          @include media-breakpoint-up(lg) {
            @include transform(translateX(-60%));
          }
          @include media-breakpoint-up(xl) {
            @include transform(translateX(-70%));
          }
          @include media-breakpoint-up(xxl) {
            @include transform(translateX(-66%));
          }

          &::after {
            content: "";
            position: absolute;
            width: 7px;
            height: 7px;
            border-top: 1px solid var(--bs-border-color);
            border-right: 1px solid var(--bs-border-color);
            top: -4px;
            right: 0;
            transform: rotate(40deg);
          }
        }
      }

      .process-step-title {
        font-size: 20px;
        font-weight: 700;
        margin-bottom: 20px;
      }

      .process-step-desc {
        font-size: 16px;
        margin-bottom: 0;
      }

      &:hover {
        background-color: var(--bs-body-bg);
        @include box-shadow(var(--wc-shadow-lg));

        .process-step-number,
        .process-step-number-alt {
          color: var(--bs-white);
          background-color: var(--wc-primary);
        }
      }
    }
  }
}
