@use "../utilities" as *;

/* Swiper Slider */
.swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  // background: transparent;
  &.slide-item-default {
    padding: 1.5rem 1.5rem;
    color: var(--bs-dark);
    font-size: 16px;
    font-weight: 700;
    background: var(--bs-gray-200);
    background-position: center;
    background-size: cover;
    text-align: center;

    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  img {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
  }
  .slide-content {
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    position: absolute;
    &::before {
      top: 0;
      left: 0;
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      background: rgba(100, 110, 120, 0.3);
    }
    .slide-content-inner {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      text-align: center;
      position: relative;
      z-index: 2;
      .slider-caption,
      .slider-title,
      .slider-desc,
      .slider-btn {
        animation-duration: 1s;
        animation-fill-mode: both;
        animation-name: fadeOutDown;
      }
    }
  }
  &.swiper-slide-active {
    .slide-content {
      .slide-content-inner {
        .slider-caption,
        .slider-title,
        .slider-desc,
        .slider-btn {
          animation-name: fadeInUp;
        }
        .slider-caption {
          animation-delay: 0.3s;
        }
        .slider-title {
          animation-delay: 0.4s;
        }
        .slider-desc {
          animation-delay: 0.5s;
        }
        .slider-btn {
          animation-delay: 0.6s;
        }
      }
    }
  }
}
.swiper-button-prev,
.swiper-button-next {
  outline: none;
  cursor: pointer;
  height: 50px;
  width: 50px;
  top: 0;
  left: 0;
  margin-top: 0;
  position: relative;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  color: var(--bs-dark);
  background-color: var(--bs-white);
  @include transition(0.3s);
  &::after {
    content: "";
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.swiper-button-next:after {
  background-image: url(./../images/shape/arrow-right.svg);
}
.swiper-button-prev:after {
  background-image: url(./../images/shape/arrow-left.svg);
}
.swiper-pagination-bullet {
  @include transition(0.3s);
}
.swiper-pagination-bullet-active {
  width: 1.25rem;
  @include border-radius(50%);
  @include transition(0.3s);
}
.swiper-pagination-horizontal {
  .swiper-pagination-bullet {
    background: var(--wc-primary);
  }
  .swiper-pagination-bullet-active {
    width: 1.5rem;
    border-radius: 50px;
    background: var(--wc-primary);
  }
}
.swiper-h {
  .swiper-slide {
    padding: 0 0;
  }
}
.swiper-images {
  .swiper-slide {
    padding: 0 0;
  }
}
.swiper-thumbs {
  height: 20%;
  box-sizing: border-box;
  padding: 0.75rem 0;
  .swiper-slide {
    width: 25%;
    height: 100%;
    opacity: 0.4;
    padding: 0 0;
  }
  .swiper-slide-thumb-active {
    opacity: 1;
  }
}
.autoplay-progress {
  position: absolute;
  right: 30px;
  bottom: 30px;
  z-index: 10;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: var(--swiper-theme-color);
  svg {
    --progress: 0;
    position: absolute;
    left: 0;
    top: 0px;
    z-index: 10;
    width: 100%;
    height: 100%;
    stroke-width: 4px;
    stroke: var(--swiper-theme-color);
    fill: none;
    stroke-dashoffset: calc(125.6px * (1 - var(--progress)));
    stroke-dasharray: 125.6;
    transform: rotate(-90deg);
  }
}

.swiper-horizontal> .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: -50px;
  @include media-breakpoint-down(xxl) {
    bottom: -40px;
  }
  @include media-breakpoint-down(xl) {
    bottom: -40px;
  }
  @include media-breakpoint-down(lg) {
    bottom: -30px;
  }
}
