@use "../utilities" as *;

/* wc-header-area */
.wc-header-area {
  // header-topbar
  .wc-header-topbar {
    position: relative;
    background-color: var(--wc-primary);
    &.is-primary-alt {
      background-color: var(--wc-primary-alt);
    }
    .header-topbar-left,
    .header-topbar-right {
      display: flex;
      align-items: center;
      .vr {
        background-color: #7e7e7e;
      }
    }
    .header-topbar-left {
      a {
        color: var(--bs-white);
        font-size: 14px;
        font-weight: 500;
        padding: 14px 16px;
        @include transition(0.3s);
        &:hover {
          color: var(--wc-brand);
        }
      }
    }
    .header-topbar-right {
      a {
        color: var(--bs-white);
        font-size: 16px;
        padding: 16px 16px;
        line-height: normal;
        align-items: baseline;
        display: flex;
        @include transition(0.3s);
        &:hover {
          color: var(--wc-brand);
        }
      }
      .nice-select {
        line-height: 22px;
        height: 34px !important;
        .current {
          color: var(--bs-white);
        }
        &::after {
          border-color: var(--bs-white);
        }
      }
    }
  }
  // start header-main
  .wc-header-main {
    height: 100px;
    display: flex;
    align-items: center;
    z-index: 1040;
    position: relative;
    box-sizing: border-box;
    background-color: var(--bs-white);
    -webkit-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    text-size-adjust: 100%;
    @include transition(0.3s);
    .wc-main-logo img {
      @include transition(0.3s);
    }
    .wc-main-menu-wrapper {
      ul.wc-main-menu {
        margin: 0;
        padding: 0;
        list-style: none;
        text-align: center;
        li {
          position: relative;
          display: inline-block;
          margin-right: 24px;
          a {
            padding: 24px 0;
            font-size: 16px;
            font-weight: 700;
            display: inline-block;
            color: var(--wc-menu-color);
            font-family: var(--wc-ff-body);
            text-decoration: none;
            @include transition(0.3s);
          }
          &.has-dropdown {
           > a {
              height: 100px;
              display: inline-flex;
              align-items: center;
              position: relative;
              &::after {
                content: "\e07c";
                font-size: 16px;
                color: var(--wc-menu-color);
                font-family: uicons-regular-rounded;
                font-weight: 500;
                margin-left: 3px;
                display: inline-block;
                @include transform(translateY(2px));
              }
            }
            &.mega-menu {
              position: static;
              .mega-menu-content {
                left: 50%;
                right: auto;
                margin: auto;
                height: auto;
                padding: 30px 30px;
                @include transform(scaleY(0) translateX(-50%));
                .mega-menu-home {
                  .mega-menu-home-item {
                    overflow: hidden;
                    position: relative;
                    @include border-radius(6px);
                    @include box-shadow(var(--wc-shadow-modern));
                    img {
                      @include transition(0.3s);
                    }
                    figcaption {
                      padding: 20px 20px;
                      position: relative;
                      background: var(--wc-white);
                    }
                    &.disabled {
                      cursor: not-allowed;
                      opacity: 0.5;
                    }
                    .mega-menu-home-item-overlay {
                      position: absolute;
                      top: 0;
                      left: 0;
                      width: 100%;
                      height: 100%;
                      background-color: rgba(0, 77, 56, 0.5);
                      display: flex;
                      align-items: center;
                      justify-content: center;
                      @include transition(0.3s);
                      opacity: 0;
                      visibility: hidden;
                      @include transform(scale(0.5));
                      @include backdrop-filter(blur(4px));
                    }
                    &:hover {
                      img {
                        @include transform(scale(1.05));
                      }
                      .mega-menu-home-item-overlay {
                        opacity: 1;
                        visibility: visible;
                        @include transform(scale(1));
                      }
                    }
                  }
                }
                .mega-menu-link {
                  .mega-menu-link-heading {
                    font-size: 14px;
                    font-weight: 700;
                    color: var(--wc-dark);
                    padding-bottom: 15px;
                    margin-bottom: 20px;
                    text-transform: uppercase;
                    position: relative;
                    border-bottom: 1px solid var(--bs-border-color);
                    &::after {
                      content: "";
                      position: absolute;
                      bottom: -2px;
                      left: 0;
                      width: 60px;
                      height: 3px;
                      background-color: var(--wc-primary);
                    }
                  }
                  .mega-menu-link-list {
                    padding-left: 0;
                    .mega-menu-link-item {
                      a {
                        padding: 25px 25px;
                        @include border-radius(4px);
                        border: 1px dashed var(--bs-border-color);
                        &::before {
                          content: none;
                        }
                        img {
                          @include transition(0.3s);
                          @include transform(scaleX(1));
                        }
                      }
                      p {
                        color: var(--bs-body-color);
                      }
                      &:hover,
                      &.active {
                        a {
                          padding-left: 25px;
                          background-color: var(--wc-light);
                          img {
                            @include transform(scaleX(-1));
                          }
                        }
                        p {
                          color: var(--bs-body-color);
                        }
                      }
                    }
                  }
                }
              }
              .mega-menu-content-full {
                width: 100%;
                max-width: 100%;
              }
              .mega-menu-content-container {
                width: 100%;
                max-width: 1296px;
                @include media-breakpoint-between(lg, xl) {
                  max-width: 936px;
                }
                @include media-breakpoint-between(xl, xxl) {
                  max-width: 1116px;
                }
                @include media-breakpoint-up(xxl) {
                  max-width: 1296px;
                }
              }
              .mega-menu-content-quad {
                width: 100%;
                max-width: 992px;
              }
              .mega-menu-content-triple {
                width: 100%;
                max-width: 768px;
              }
              .mega-menu-content-double {
                width: 100%;
                max-width: 576px;
              }
            }
          }
          .sub-menu {
            left: 0;
            top: 100%;
            width: 260px;
            text-align: left;
            padding: 20px 20px;
            position: absolute;
            background: var(--wc-white);
            z-index: 99;
            opacity: 0;
            visibility: hidden;
            @include border-radius(0 0 12px 12px);
            @include box-shadow(0 25px 50px -5px rgba(0, 0, 0, 0.075));
            @include transition-alt(all 0.4s ease);
            @include transform(scaleY(0));
            @include transform-origin(0 0 0);

            li {
              margin: 0;
              opacity: 0;
              width: 100%;
              display: inline-block;
              @include transform(translateY(10px));
              @include transition-alt(opacity 0.35s ease, transform 0.35s ease);
              a {
                z-index: 1;
                width: 100%;
                font-size: 16px;
                font-weight: 600;
                padding: 10px 20px;
                position: relative;
                color: var(--wc-menu-color);
                &::before {
                  top: 20px;
                  left: 20px;
                  width: 5px;
                  height: 5px;
                  content: "";
                  z-index: -1;
                  position: absolute;
                  background-color: var(--wc-primary-alt);
                  @include border-radius(50px);
                  @include transition(0.3s);
                  opacity: 0;
                  visibility: hidden;
                  @include transform(scale(0));
                }
              }
              &.has-dropdown {
               > a {
                  height: auto;
                  justify-content: space-between;
                  &::after {
                    font-size: 10px;
                    content: "\e07b";
                  }
                }
                .sub-menu {
                  top: 0;
                  left: 240px;
                  right: auto;
                  opacity: 0;
                  visibility: hidden;
                  padding-left: 20px;
                  padding-bottom: 20px;
                  @include border-radius(12px 12px);
                }
                &.left-menu {
                  .sub-menu {
                    right: 240px;
                    left: auto;
                  }
                }
              }
              &:focus,
              &:hover,
              &.active {
               > a {
                  padding-left: 35px;
                  color: var(--wc-primary);
                  background-color: var(--wc-light);
                  @include border-radius(2px);
                  @include transition(0.3s);
                  &::before {
                    z-index: 1;
                    opacity: 1;
                    visibility: visible;
                    @include transform(scale(1));
                  }
                }
               > .sub-menu {
                  opacity: 1;
                  visibility: visible;
                }
              }
             > a.active {
                padding-left: 35px;
                color: var(--wc-primary);
                background-color: var(--wc-light);
                @include border-radius(2px);
                @include transition(0.3s);
                &::before {
                  z-index: 1;
                  opacity: 1;
                  visibility: visible;
                  @include transform(scale(1));
                }
              }
            }
          }
          &:focus,
          &:hover,
          &:active {
           > a {
              color: var(--wc-primary);
              &::after {
                color: var(--wc-primary);
              }
            }
          }
          &.active {
           > a {
              color: var(--wc-primary);
              &::after {
                color: var(--wc-primary);
              }
            }
          }
        }
      }
    }

    .wc-header-extra {
      text-align: right;
      .wc-header-button-group {
        display: flex;
        align-items: center;
        justify-content: end;
        gap: 12px;
        .wc-header-call-btn {
          text-align: left;
          line-height: 1.75;
          color: var(--wc-menu-color);
          text-decoration: none;
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 12px;
          .wc-call-btn-text {
            .wc-call-btn-text-top {
              font-size: 11px;
              font-weight: 500;
              color: var(--bs-body-color);
            }
            .wc-call-btn-number {
              font-size: 20px;
              font-weight: 700;
              color: var(--wc-primary);
              font-family: var(--wc-ff-number);
            }
          }
        }
      }
    }

    &.sticky {
      top: 0;
      left: 50%;
      width: 100%;
      position: fixed;
      background-color: var(--bs-white);
      box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.1);
      animation-name: stickyTransition;
      animation-duration: 0.5s;
      animation-fill-mode: both;
      // sticky-transition
      @-webkit-keyframes stickyTransition {
        from {
          opacity: 0;
          @include transform(translate3d(0, -100%, 0));
        }

        to {
          opacity: 1;
          @include transform(translate3d(0, 0, 0));
        }
      }
      @keyframes stickyTransition {
        from {
          opacity: 0;
          @include transform(translate3d(0, -100%, 0) translateX(-50%));
        }

        to {
          opacity: 1;
          @include transform(translate3d(0, 0, 0) translateX(-50%));
        }
      }
    }

    &.is-transparent {
      background-color: rgba(255, 255, 255, 0.1);
      .wc-main-menu-wrapper {
        ul.wc-main-menu {
          li {
            a {
              color: var(--bs-white);
            }
            &.has-dropdown {
             > a {
                &::after {
                  color: var(--bs-white);
                }
              }
            }
          }
        }
      }
      .wc-header-extra {
        .wc-btn {
          &.wc-btn-light {
            color: var(--wc-white);
            background-color: rgb(58 127 102);
            &:hover {
              color: var(--wc-white);
            }
          }
        }
        .wc-header-call-btn {
          .wc-call-btn-text {
            .wc-call-btn-text-top {
              color: var(--bs-gray-200);
            }
            .wc-call-btn-number {
              color: var(--wc-white);
            }
          }
        }
      }
      &.sticky {
        background-color: var(--bs-white);
      }
    }

    &.is-boxed {
      width: 100%;
      max-width: 1296px;
      margin-right: auto;
      margin-left: auto;
      @include border-radius(0 0 12px 12px);
    }
  }
}

#InfoCanvas {
  .offcanvas-header,
  .offcanvas-body {
    padding: 36px 32px;
  }
}

// Responsive Header
@include media-breakpoint-up(lg) {
  .wc-header-area {
    .wc-header-main {
      .wc-main-menu-wrapper {
        ul.wc-main-menu {
          li {
            &:focus,
            &:hover,
            &:active {
             > .sub-menu {
                opacity: 1;
                visibility: visible;
                @include transform(scaleY(1));
                li {
                  opacity: 1;
                  @include transform(translateY(0));
                  // Animate on parent hover
                  @for $i from 1 through 20 {
                    &:nth-child(#{$i}) {
                      transition-delay: #{0.1 * $i}s;
                    }
                  }
                }
              }
              &.mega-menu {
                .mega-menu-content {
                  @include transform(scaleY(1) translateX(-50%));
                }
              }
            }
          }
        }
      }
    }
  }
}
@include media-breakpoint-down(lg) {
  .wc-header-area {
    .wc-header-main {
      .wc-main-menu-wrapper {
        top: 0;
        left: 0;
        bottom: 0;
        z-index: 1050;
        position: fixed;
        width: 320px;
        height: 100%;
        min-height: 100vh;
        background: var(--wc-white);
        overflow-y: scroll;
        overflow-x: hidden;
        scrollbar-width: thin;
        opacity: 0;
        visibility: hidden;
        @include transform(translateX(-100%));
        @include transition(0.3s);
        &.open {
          opacity: 1;
          visibility: visible;
          @include transform(translateX(0));
        }
        .wc-mobile-menu-header {
          .wc-mobile-logo {
            padding: 36px 32px;
            border-bottom: 1px solid var(--wc-border);
          }
        }
        ul.wc-main-menu {
          text-align: left;
          padding: 32px 32px;
          flex-direction: column;
          display: flex;
          li {
            margin-right: 0;
            a {
              padding: 12px 0;
            }
            &.has-dropdown {
              position: relative;
             > a {
                height: auto;
                width: 100%;
                &::after {
                  margin-left: auto;
                }
              }
              .sub-menu {
                padding: 0 0;
                display: none;
                position: unset;
                @include box-shadow(none);
                li {
                  opacity: 1;
                }
                // li {
                //   a {
                //     padding: 0 0;
                //   }
                //   &:hover> a {
                //     &::before,
                //     &::after {
                //       content: none;
                //     }
                //   }
                // }
              }
              &.active .sub-menu {
                padding-bottom: 20px;
                opacity: 1;
                visibility: visible;
                @include transform(scale(1));
              }
              &.mega-menu {
                .mega-menu-content {
                  padding: 16px 0;
                  transform: none;
                  .mega-menu-link {
                    .mega-menu-link-heading {
                      margin-bottom: 0px;
                      margin-left: 20px;
                    }
                  }
                }
                .mega-menu-content-container,
                .mega-menu-content-double,
                .mega-menu-content-triple {
                  max-width: 100%;
                }
              }
            }
          }
        }
        .wc-mobile-menu-footer {
          padding: 24px 32px;
          border-top: 1px solid var(--wc-border);
        }
      }
      .wc-backdrop {
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        min-height: 100vh;
        z-index: 1049;
        display: none;
        position: fixed;
        background-color: rgba(0, 77, 56, 0.5);
        @include backdrop-filter(blur(10px));
      }
      &.is-boxed {
        width: 100%;
        max-width: 100%;
        @include border-radius(0 0);
      }
      &.is-transparent {
        .wc-main-menu-wrapper {
          ul.wc-main-menu {
            li {
              a {
                color: var(--wc-menu-color);
              }
              &.has-dropdown {
               > a {
                  &::after {
                    color: var(--wc-menu-color);
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
