@use "../utilities" as *;

/*------------- wc-hero-area-one ------------ */
.wc-hero-area-one {
  height: 960px;
  position: relative;
  @include media-breakpoint-down(sm) {
    height: 720px;
  }
  // strat lick-slider
  .wc-slick-slider {
    margin: auto;
    width: 100%;
    height: 100%;
    overflow: hidden;
    .wc-slide-item {
      width: 100%;
      height: 100%;
      position: relative;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      &::after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        background: rgba(0, 90, 65, 0.5) url(./../images/shape/shape-dots-grid.png); /* url(./../images/shape/shape-dots-grid.png) */
      }
      .wc-slider-image-animation {
        background-size: cover;
        background-position: center;
        @include filter(grayscale(1));
        @include transform(translate3d(0, 0, 0) scale(1));
        @include transition(15s);
      }
      .wc-slide-content {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        &::before,
        &::after {
          top: 50%;
          left: 0%;
          z-index: 0;
          content: "";
          position: absolute;
          @include border-radius(100%);
          @include animation(none);
          @include transform-origin(center center);
        }
        &::before {
          width: 1100px;
          height: 1100px;
          background: rgba(0, 77, 56, 0.1);
          @include media-breakpoint-down(lg) {
            width: 600px;
            height: 600px;
          }
          @include media-breakpoint-down(md) {
            width: 400px;
            height: 400px;
            background: rgba(0, 77, 56, 0.2);
          }
          @include media-breakpoint-down(sm) {
            width: 300px;
            height: 300px;
            background: rgba(0, 77, 56, 0.3);
          }
        }
        &::after {
          width: 1400px;
          height: 1400px;
          background: rgba(0, 77, 56, 0.2);
          @include media-breakpoint-down(lg) {
            width: 1000px;
            height: 1000px;
          }
          @include media-breakpoint-down(md) {
            width: 700px;
            height: 700px;
            background: rgba(0, 77, 56, 0.3);
          }
          @include media-breakpoint-down(sm) {
            width: 500px;
            height: 500px;
            background: rgba(0, 77, 56, 0.4);
          }
        }
        .wc-slide-content-inner {
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          position: relative;
          z-index: 2;
          @include media-breakpoint-down(sm) {
            height: 85%;
          }

          .wc-slider-caption,
          .wc-slider-title,
          .wc-slider-desc,
          .wc-slider-btn {
            animation-duration: 1.5s;
            animation-fill-mode: both;
            animation-name: fadeOutDown;
          }
        }
      }
      &.slick-active {
        .wc-slider-image-animation {
          transform: translate3d(0, 0, 0) scale(1.2);
        }

        .wc-slide-content {
          .wc-slide-content-inner {
            .wc-slider-caption,
            .wc-slider-title,
            .wc-slider-desc,
            .wc-slider-btn {
              animation-name: fadeInUp;
              animation-fill-mode: both;
            }

            .wc-slider-caption {
              color: #ffffff;
              animation-delay: 0.5s;
              animation-duration: 0.5s;
            }

            .wc-slider-title {
              animation-delay: 0.7s;
              animation-duration: 0.7s;
            }

            .wc-slider-desc {
              animation-delay: 0.9s;
              animation-duration: 0.9s;
            }

            .wc-slider-btn {
              animation-delay: 1s;
              animation-duration: 1s;
            }

            @keyframes fadeInUp {
              0% {
                opacity: 0;
                -webkit-transform: translateY(25px);
                -ms-transform: translateY(25px);
                transform: translateY(25px);
              }

              100% {
                opacity: 1;
                -webkit-transform: translateY(0);
                -ms-transform: translateY(0);
                transform: translateY(0);
              }
            }
          }
        }
      }
      &.slick-current {
        .wc-slide-content {
          &::before {
            @include animation(scaleUpCircle 1.5s ease-out forwards);
          }
          &::after {
            @include animation(scaleUpCircle 2s ease-out forwards);
          }
        }
      }
      @keyframes scaleUpCircle {
        0% {
          opacity: 0;
          @include transform(translate(-50%, -50%) scale(0.5));
        }
        50% {
          opacity: 0.6;
          @include transform(translate(-50%, -50%) scale(1.01));
        }
        100% {
          opacity: 1;
          @include transform(translate(-50%, -50%) scale(1));
        }
      }
    }
    .slick-prev,
    .slick-next {
      left: 0px;
      top: auto;
      bottom: -60px;
      padding: 40px;
      width: 120px;
      height: 120px;
      z-index: 99;
      position: absolute;
      background-color: var(--bs-primary);
      @include transition(0.3s);
      &::before {
        content: none;
        color: var(--bs-dark);
        font-family: "uicons-regular-rounded" !important;
      }
    }
    .slick-next {
      left: 120px;
    }
    .slick-prev,
    .slick-next {
      &:hover {
        background-color: var(--wc-brand);
      }
    }
  }
  // wc-slider-progress-container
  .wc-slider-progress-container {
    left: 320px;
    bottom: 45px;
    z-index: 20;
    position: absolute;
    @include media-breakpoint-down(sm) {
      display: none;
    }
    .wc-slider-number {
      font-size: 16px;
      font-weight: 700;
      font-family: var(--wc-ff-number);
      color: var(--bs-white);
      margin-bottom: 6px;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 12px;
    }
    .wc-slider-progress {
      width: 100px;
      height: 3px;
      background: rgba(255, 255, 255, 0.3);
      overflow: hidden;
      position: relative;
    }
    .wc-slider-progress-bar {
      width: 0%;
      height: 100%;
      background: var(--bs-white);
      @include transition-alt(width 0.4s linear);
    }
  }
  // wc-follow-us
  .wc-follow-us {
    left: 0;
    top: 50%;
    width: 60px;
    height: auto;
    position: absolute;
    writing-mode: sideways-lr;
    @include transform(translateY(-50%));
    display: none;
    align-items: end;
    justify-content: center;
    @include media-breakpoint-up("3xl") {
      display: flex;
    }
    a {
      font-size: 13px;
      font-weight: 700;
      text-transform: uppercase;
      color: var(--bs-white);
      margin-bottom: 24px;
      letter-spacing: 1px;
      @include transition(0.3s);
      &:hover {
        color: var(--wc-brand);
      }
    }
  }
  // wc-hero-big-text
  .wc-hero-big-text {
    top: 50%;
    right: 0;
    width: 200px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    writing-mode: sideways-lr;
    background-color: var(--wc-brand);
    @include transform(translateY(-50%));
  }
}

/*------------- wc-key-features-area ------------ */
.wc-key-features-area {
  width: 100%;

  .key-feature-item-01,
  .key-feature-item-02,
  .key-feature-item-03,
  .key-feature-item-04 {
    overflow: hidden;
    position: relative;
    .key-feature-content {
      gap: 20px;
      display: flex;
      align-items: center;
      padding: 30px 15px;
      margin-bottom: -75px;
      @include media-breakpoint-up(sm) {
        gap: 25px;
        padding: 45px 30px 0px;
        margin-bottom: -30px;
      }
      @include media-breakpoint-up(lg) {
        padding: 60px 45px 0px;
        margin-bottom: -10px;
      }

      .key-feature-icon {
        flex-shrink: 0;
        @include transform(scaleX(1));
        @include transition(0.3s);
      }
    }
    .wc-key-features-link {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      padding: 25px 30px;
      background: var(--wc-primary);
      color: var(--bs-white);
      opacity: 0;
      visibility: hidden;
      @include transform(translateY(100%));
      @include transition(0.5s);
      .wc-key-features-text {
        font-size: 13px;
        text-transform: uppercase;
      }
      .wc-key-features-link-icon {
        height: 100%;
        width: 75px;
        color: var(--bs-dark);
        background: var(--wc-brand);
        position: absolute;
        right: 0;
      }
    }
    &::before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      background: rgba(0, 77, 56, 0.1);
      opacity: 0;
      visibility: hidden;
      @include transition(0.4s);
      @include transform(translateY(100%));
    }
    &:hover {
      .key-feature-icon {
        @include transform(scaleX(-1));
      }
      .wc-key-features-link {
        opacity: 1;
        visibility: visible;
        @include transform(translateY(0));
      }
      &::before {
        opacity: 1;
        visibility: visible;
        @include transition(0.4s);
        @include transform(translateY(0));
      }
    }
  }

  .key-feature-item-01 {
    border-left: 1px solid var(--wc-border);
    border-bottom: 1px solid var(--wc-border);
    @include media-breakpoint-up(xxl) {
      border-bottom: none;
    }
  }

  .key-feature-item-02 {
    border-left: 1px solid var(--wc-border);
    border-right: 1px solid var(--wc-border);
    border-bottom: 1px solid var(--wc-border);
    @include media-breakpoint-up(xxl) {
      border-right: none;
      border-bottom: none;
    }
  }
  .key-feature-item-03 {
    border-left: 1px solid var(--wc-border);
    border-bottom: 1px solid var(--wc-border);
    @include media-breakpoint-up(xl) {
      border-bottom: none;
    }
    @include media-breakpoint-up(xxl) {
      border-right: 1px solid var(--wc-border);
      border-bottom: none;
    }
  }

  .key-feature-item-04 {
    border-left: 1px solid var(--wc-border);
    border-right: 1px solid var(--wc-border);
    @include media-breakpoint-up(xxl) {
      border-left: none;
    }
  }
}

/*------------- wc-about-area ------------ */
.wc-about-area {
  background-size: auto;
  background-repeat: no-repeat;
  background-position: center right;
  background-image: url(./../images/shape/shape-wave-line.png);

  .wc-about-experience-col {
    position: relative;
    .image-reveal-effect {
      border-bottom-right-radius: 160px;
    }
    .wc-about-experience-wrap {
      top: 50%;
      left: 100%;
      display: block;
      position: absolute;
      @include transform(translate(-50%, -50%));
      @include transition(0.3s);
      @include media-breakpoint-down("xxl") {
        display: none;
      }
      .wc-experience-figure {
        width: 300px;
        height: 300px;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        position: relative;
        overflow: hidden;
        @include border-radius(50%);
        @include media-breakpoint-down(sm) {
          width: 250px;
          height: 250px;
        }
        @include media-breakpoint-between(lg, xl) {
          width: 250px;
          height: 250px;
        }
        &::before,
        &::after {
          top: 0;
          left: 0;
          content: "";
          position: absolute;
          @include border-radius(50%);
          @include backdrop-filter(blur(10px));
        }
        &::before {
          z-index: 1;
          margin: 50px;
          width: 200px;
          height: 200px;
          background: var(--bs-white);
          @include media-breakpoint-down(sm) {
            width: 150px;
            height: 150px;
          }
          @include media-breakpoint-between(lg, xl) {
            width: 150px;
            height: 150px;
          }
        }
        &::after {
          width: 300px;
          height: 300px;
          background: rgba(235, 242, 242, 0.75);
          @include media-breakpoint-down(sm) {
            width: 250px;
            height: 250px;
          }
          @include media-breakpoint-between(lg, xl) {
            width: 250px;
            height: 250px;
          }
        }
      }
    }
  }

  .wc-about-list-item {
    .wc-about-list-icon {
      img {
        @include transition(0.3s);
      }
    }

    &:hover {
      .wc-about-list-icon {
        img {
          @include transform(scaleX(-1));
        }
      }
    }
  }
}

/*------------- wc-services-area ------------ */
.wc-services-area {
  background-image: url(./../images/shape/vr-line-sm-primary.svg);
  .services-stroke-text {
    left: 0;
    top: 120px;
    font-weight: 800;
    position: absolute;
    white-space: nowrap;
    writing-mode: sideways-lr;
    @include transform(translateY(-50%));
  }
  .wc-services-swiper {
    padding-top: 120px;
    padding-bottom: 120px;
    padding-left: 24px;
    background-color: var(--wc-primary-alt);
    background-image: url(./../images/shape/shape-zigzak.png);
    background-repeat: repeat-x;
    background-position: bottom right;
    @include media-breakpoint-up(lg) {
      margin-right: -42vw;
    }
    @include media-breakpoint-up(xl) {
      margin-right: -24vw;
    }
    @include media-breakpoint-up(xxl) {
      margin-right: -60vw;
    }
    @include media-breakpoint-down(xl) {
      padding-top: 60px;
      padding-bottom: 60px;
    }
    @include media-breakpoint-down(lg) {
      padding-left: 0px;
      padding-top: 0px;
      padding-bottom: 60px;
      background-color: var(--wc-primary);
    }
    .service-item {
      .service-content-body {
        position: relative;
        &::before {
          width: 30px;
          height: 30px;
          content: "";
          bottom: 0;
          left: 0;
          position: absolute;
          background-color: var(--wc-light);
          @include transition(0.3s);
        }
      }
      .card-body {
        z-index: 3;
        position: relative;
        .service-number {
          top: 35px;
          left: -30px;
          width: 65px;
          height: 65px;
          position: absolute;
          background: var(--bs-body-bg);
          display: flex;
          align-items: center;
          justify-content: center;
          @include border-radius(50%);
          @include transition(0.3s);
          @include media-breakpoint-down(md) {
            display: none;
          }
        }
      }
      &:hover {
        .service-content-body {
          &::before {
            width: 100%;
            height: 100%;
          }
          .service-number {
            background-color: var(--wc-light);
          }
        }
      }
    }
  }
}

/*------------- wc-companies-area ------------ */
.wc-companies-area {
  .brand-logo-item {
    display: block;
    @include transition(0.3s);

    img {
      @include transition(0.3s);
      @include filter(brightness(0.75));
    }

    &:hover {
      border-color: transparent !important;
      @include box-shadow(var(--wc-shadow-lg));
      @include transition(0.3s);

      img {
        @include filter(brightness(0));
        @include transform(scale(1.05));
      }
    }
  }
}

/*------------- wc-call-to-action-area ------------ */
.wc-call-to-action-area {
  // wc-call-to-action-top
  .wc-call-to-action-top {
    min-height: 600px;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-image: url(./../images/bg/bg-banner-01.jpg);
    display: flex;
    align-items: center;
    justify-content: center;

    .wc-call-to-play-btn {
      width: 120px;
      height: 120px;
      color: var(--wc-dark);
      background-color: var(--wc-brand);
      display: flex;
      align-items: center;
      justify-content: center;
      @include border-radius(50%);

      &.pulse-effect {
        isolation: isolate;
        transition: all 0.25s cubic-bezier(0, 0, 0.31, 0.99);
        animation: pulse-effect 2s infinite;

        @keyframes pulse-effect {
          0% {
            box-shadow: 0 0 0 0 rgb(255, 255, 255, 0.5);
          }

          70% {
            box-shadow: 0 0 0 25px rgb(255, 255, 255, 0);
          }

          100% {
            box-shadow: 0 0 0 0 rgb(255, 255, 255, 0);
          }
        }
      }
    }
  }

  // wc-call-to-action-bottom
  .wc-call-to-action-bottom {
    position: relative;
    background-color: var(--wc-light);
    background-image: url(./../images/shape/shape-church-noise.png);

    .card {
      top: 250px;
      margin-top: -120px;
      @include media-breakpoint-up(sm) {
        margin-top: -121px;
      }
      @include media-breakpoint-up(md) {
        margin-top: -123px;
      }
      @include media-breakpoint-up(lg) {
        margin-top: -125px;
      }
      @include media-breakpoint-up(xl) {
        margin-top: -126px;
      }
    }
  }

  // wc-industies-help
  .wc-industies-help {
    .wc-industies-item {
      .wc-industies-item-icon {
        @include transition(0.3s);
      }

      &:hover {
        a {
          background-color: var(--wc-brand);
        }

        .wc-industies-item-icon {
          @include transform(scaleX(-1));
        }

        .wc-industies-item-title {
          color: var(--wc-primary);
        }
      }
    }
  }
}

/*------------- wc-marquee-slider-brand ------------ */
.wc-marquee-slider-brand {
  background-color: var(--wc-brand);
  position: relative;
  &::before,
  &::after {
    top: 0;
    content: "";
    position: absolute;
    width: 330px;
    height: 100%;
    z-index: 2;
    @include media-breakpoint-down(lg) {
      width: 200px;
    }
    @include media-breakpoint-down(md) {
      width: 130px;
    }
  }
  &::before {
    left: 0;
    background: linear-gradient(90deg, var(--wc-brand) 0%, var(--wc-brand) 10%, #ffffff00 100%);
  }
  &::after {
    right: 0;
    background: linear-gradient(270deg, var(--wc-brand) 0%, var(--wc-brand) 10%, #ffffff00 100%);
  }
  .marquee-slider {
    .marquee-group {
      &.bg-dot-style {
        .marquee-item {
          .marquee-icon {
            background-color: var(--wc-primary);
          }
          .marquee-text {
            color: var(--wc-primary);
          }
          &:nth-child(odd) {
            .marquee-text {
              mix-blend-mode: normal;
              color: transparent;
              stroke-width: 1px;
              -webkit-text-stroke-width: 1px;
              -webkit-text-stroke-color: var(--wc-primary);
            }
          }
        }
      }
    }
  }
}
