@use "../utilities" as *;

// page-title-section
.page-title-section {
  position: relative;
  padding: 60px 0 120px;
  &::before {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: "";
    position: absolute;
    mix-blend-mode: normal;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    filter: grayscale(1);
  }
  &::after {
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    content: "";
    position: absolute;
    mix-blend-mode: normal;
    background: url(../images/shape/shape-page-title-pattern.png), linear-gradient(to right, rgba(0, 77, 56, 0.9) 30%, transparent);
    background-size: cover;
  }
  &.page-title-banner-01 {
    &::before {
      background-image: url(./../images/bg/bg-banner-01.jpg);
    }
  }
  &.page-title-banner-02 {
    &::before {
      background-image: url(./../images/bg/bg-banner-02.jpg);
    }
  }
  &.page-title-banner-03 {
    &::before {
      background-image: url(./../images/bg/bg-banner-03.jpg);
    }
  }
  &.page-title-banner-04 {
    &::before {
      background-image: url(./../images/bg/bg-banner-04.jpg);
    }
  }
  &.page-title-banner-05 {
    &::before {
      background-image: url(./../images/bg/bg-banner-05.jpg);
    }
  }
  &.page-title-banner-06 {
    &::before {
      background-image: url(./../images/bg/bg-banner-06.jpg);
    }
  }
  @include media-breakpoint-up(md) {
    padding: 80px 0 140px;
  }
  @include media-breakpoint-up(xl) {
    padding: 120px 0 180px;
  }
  .section-title {
    font-weight: 700;
  }
  .page-title-nav-container {
    bottom: 0;
    z-index: 2;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 15px;
    position: absolute;
    @include transition(0.3s);
    background-color: rgba(0, 0, 0, 0.05);
    @include backdrop-filter(blur(4px));
    .breadcrumb {
      .breadcrumb-item {
        & + .breadcrumb-item {
          padding-left: 12px;
          &::before {
            // line-height: 2.75;
            padding-right: 12px;
          }
        }
        &.active {
          color: var(--wc-light-alt);
        }
      }
    }
  }
}
